import styles from "./AssociationPanel.module.css";

import { useContext, useEffect } from "react";
import { Route } from "react-router-dom";

import PanelNav from "./Nav/PanelNav";

import CreateListing from "./CreateListing/CreateListing";
import Dashboard from "./Dashboard/Dashboard";
import EditListing from "./EditListing/EditListing";
import Listings from "./Listings/Listings";
import AssoProfile from "./Profile/AssoProfile";
import Settings from "./Settings/Settings";
import Help from "./Help/Help";

import { AssociationContext } from "./../../../../contexts/associations/assoContext";
import { currentPageContext } from "./../../../../contexts/shared/currentPageContext";
import api from "./../../../../services/api";

const AssociationPanel = () => {
  const { setAssociation } = useContext(AssociationContext);
  const { setCurrentPage } = useContext(currentPageContext);

  useEffect(() => {
    api.get(`/association`).then((res) => {
      setAssociation(res.data);
    });
  }, [setAssociation]);

  useEffect(() => {
    setCurrentPage("association");
  }, []);

  return (
    <div className={styles.panel}>
      <PanelNav />
      <div className={styles.content}>
        <Route
          exact
          path="/associations/panel/dashboard"
          component={Dashboard}
        />

        <Route exact path="/associations/panel/listings" component={Listings} />
        <Route
          exact
          path="/associations/panel/listing/edit/:id"
          component={EditListing}
        />

        <Route
          exact
          path="/associations/panel/create-listing"
          component={CreateListing}
        />
        <Route
          exact
          path="/associations/panel/profile"
          component={AssoProfile}
        />
        <Route exact path="/associations/panel/settings" component={Settings} />
        <Route exact path="/associations/panel/help" component={Help} />
      </div>
    </div>
  );
};

export default AssociationPanel;
