import styles from "./Header.module.css";
import { Link } from "react-router-dom";
import { useState, useContext } from "react";
import { ReactComponent as UserIcon } from "./user.svg";
import { ReactComponent as Arrow } from "./chevron-down.svg";

import Cookies from "js-cookie";
import { useDetectClickOutside } from "react-detect-click-outside";
import api from "./../../../services/api";

import authContext from "./../../../contexts/authContext";
import userContext from "../../../contexts/userContext";
import notificationContext from "../../../contexts/notificationContext";
import { currentPageContext } from "../../../contexts/shared/currentPageContext";

const Header = () => {
  const { currentPage } = useContext(currentPageContext);
  const [isActive, setIsActive] = useState(false);
  const [dropDownIsActive, setDropDownIsActive] = useState("");
  const { setNotification } = useContext(notificationContext);

  const { isAuthenticated, setIsAuthenticated } = useContext(authContext);
  const { setUser } = useContext(userContext);

  const handleLogout = () => {
    api
      .post("/logout")
      .then((res) => {
        if (res) {
          Cookies.remove("x-access-token");
          Cookies.remove("x-refresh-token");
          Cookies.remove("authenticated");
          Cookies.remove("hasAssociation");
          Cookies.remove("association_uuid");
          Cookies.remove("association_id");
          setIsAuthenticated(false);
          setUser({});
          setNotification({
            state: "shown",
            type: "success",
            message: "Vous avez t dconnect",
          });
        }
      })
      .catch((error) => {
        setNotification({
          state: "shown",
          type: "error",
          message: error.response.data.error,
        });
      });
    setDropDownIsActive(false);
  };

  const dropDown = () => {
    if (dropDownIsActive === "") {
      setDropDownIsActive(true);
    } else if (dropDownIsActive === true) {
      setDropDownIsActive(false);
    } else {
      setDropDownIsActive(true);
    }
  };

  const ref = useDetectClickOutside({
    onTriggered: () => setDropDownIsActive(false),
  });

  let hasAssociation = Cookies.get("hasAssociation");

  return (
    <>
      <header className={styles.header}>
        <div className={styles.headerNavLeft}>
          <div
            onClick={() => setIsActive(!isActive)}
            className={`${styles.hamburger} ${isActive ? styles.active : ""}`}
          >
            <div className={styles.hamburgerLine1}></div>
            <div className={styles.hamburgerLine2}></div>
            <div className={styles.hamburgerLine3}></div>
          </div>
          <Link to="/" className={styles.headerLogo}>
            Anima<span>Z</span>one
          </Link>
        </div>

        <nav className={styles.headerNav}>
          <Link
            to="/home"
            className={
              currentPage === "home"
                ? styles.headerLink + " " + styles.currentPage
                : styles.headerLink
            }
          >
            <span>Accueil</span>
          </Link>
          <Link
            to="/adopt-me"
            className={
              currentPage === "adopt-me"
                ? styles.headerLink + " " + styles.currentPage
                : styles.headerLink
            }
          >
            <span>Adoptez-moi !</span>
          </Link>
          <Link
            to="/about"
            className={
              currentPage === "about"
                ? styles.headerLink + " " + styles.currentPage
                : styles.headerLink
            }
          >
            <span>A propos</span>
          </Link>

          {hasAssociation === "true" ? (
            <Link
              to="/associations/panel/dashboard"
              className={
                currentPage === "association"
                  ? styles.headerLink + " " + styles.currentPage
                  : styles.headerLink
              }
            >
              <span>Mon asso</span>
            </Link>
          ) : null}
        </nav>
        <div className={styles.profile} ref={ref}>
          <UserIcon className={styles.headerProfile} onClick={dropDown} />

          {(!isAuthenticated && (
            <nav
              className={`${styles.profileNav} ${
                dropDownIsActive === true ? styles.dropDownActive : ""
              } ${dropDownIsActive === false ? styles.dropDownInactive : ""}`}
            >
              <Link to="/login" onClick={() => setDropDownIsActive(false)}>
                Se connecter
              </Link>
              <Link to="/register" onClick={() => setDropDownIsActive(false)}>
                S'enregistrer
              </Link>
              <Arrow
                className={styles.profileArrow}
                onClick={() => setDropDownIsActive(false)}
              />
            </nav>
          )) || (
            <nav
              className={`${styles.profileNav} ${
                dropDownIsActive === true ? styles.dropDownActive : ""
              } ${dropDownIsActive === false ? styles.dropDownInactive : ""}`}
            >
              <Link to="/account" onClick={() => setDropDownIsActive(false)}>
                Mon compte
              </Link>
              <a onClick={handleLogout}>Se dconnecter</a>
              <Arrow
                className={styles.profileArrow}
                onClick={() => setDropDownIsActive(false)}
              />
            </nav>
          )}
        </div>
      </header>
      <nav
        className={`${styles.mobileNav} ${
          isActive ? styles.showMobileNav : null
        }`}
      >
        <Link to="/home" className={styles.headerLink}>
          <span>Accueil</span>
        </Link>
        <Link to="/adopt-me" className={styles.headerLink}>
          <span>Adoptez-moi !</span>
        </Link>
        <Link to="/about" className={styles.headerLink}>
          <span>A propos</span>
        </Link>
        {hasAssociation === "true" ? (
          <Link
            to="/associations/panel/dashboard"
            className={
              currentPage === "association"
                ? styles.headerLink + " " + styles.currentPage
                : styles.headerLink
            }
          >
            <span>Mon asso</span>
          </Link>
        ) : null}
      </nav>
    </>
  );
};

export default Header;
