import AnimalCard from "./../../shared/AnimalCard/AnimalCard";
import styles from "./AdoptMe.module.css";
const Results = ({ listings, loading, query }) => {
  if (loading) {
    return <h2>Loading...</h2>;
  }

  return (
    <div className={styles.cardGrid}>
      {listings.length === 0 && (
        <h2 className={styles.noResults}>
          Aucun rsultats de recherche pour "{query}"
        </h2>
      )}
      {listings.length > 0 &&
        listings.map((listing) => {
          return (
            <AnimalCard
              key={listing.id}
              id={listing.id}
              category={listing.category}
              name={listing.name}
              gender={listing.gender}
              birthDate={listing.birth_date}
              city={listing.City}
              about1={listing.about1}
              about2={listing.about2}
              about3={listing.about3}
              link={`listing/${listing.id}`}
              imageLink={listing.image1}
            />
          );
        })}
    </div>
  );
};

export default Results;
