import styles from "./Listing.module.css";
import LikeIcon from "../../../shared/LikeIcon/LikeIcon";
import ListingShape from "./ListingShape";
import { Link } from "react-router-dom";
import { useState, useEffect, useContext } from "react";
import { useParams } from "react-router";
import Moment from "react-moment";
import "moment/locale/fr";
import { currentPageContext } from "./../../../../contexts/shared/currentPageContext";
import api from "./../../../../services/api";

const Listing = () => {
  const [data, setData] = useState("");
  const [associationData, setAssociationData] = useState("");
  let { id } = useParams();

  const { setCurrentPage } = useContext(currentPageContext);

  useEffect(async () => {
    await api.get(`/listing/${id}`).then(async (res) => {
      setData(res.data);
      await api
        .get(`/associations/public/${res.data.association_id}`)
        .then((res) => {
          setAssociationData(res.data);
        });
    });
  }, [setData, setAssociationData]);

  useEffect(async () => {
    setCurrentPage("");
  }, []);

  const [shown, setShown] = useState("image1");

  return (
    <div className={styles.content}>
      <div className={styles.shape}>
        <ListingShape
          className={`${data.category === "cat" ? styles.catShape : null} ${
            data.category === "dog" ? styles.dogShape : null
          } ${data.category === "rabbit" ? styles.rabbitShape : null} ${
            data.category === "bird" ? styles.birdShape : null
          } ${data.category === "rodent" ? styles.rodentShape : null}`}
        />
      </div>
      <div className={styles.listing}>
        <div className={styles.images}>
          <div className={styles.imageColumn}>
            <div
              className={styles.columnImage}
              onClick={() => setShown("image1")}
            >
              <img src={data.image1} alt={`${data.name} 1`} />
            </div>
            <div
              className={styles.columnImage}
              onClick={() => setShown("image2")}
            >
              <img src={data.image2} alt={`${data.name} 2`} />
            </div>
            <div
              className={styles.columnImage}
              onClick={() => setShown("image3")}
            >
              <img src={data.image3} alt={`${data.name} 3`} />
            </div>
          </div>
          <div className={styles.imageShown}>
            <img
              src={data.image1}
              className={shown === "image1" ? styles.visible : styles.hidden}
              alt={`${data.name}`}
            />
            <img
              src={data.image2}
              className={shown === "image2" ? styles.visible : styles.hidden}
              alt={`${data.name}`}
            />
            <img
              src={data.image3}
              className={shown === "image3" ? styles.visible : styles.hidden}
              alt={`${data.name}`}
            />
          </div>
        </div>
        <div className={styles.listingHeader}>
          <div className={styles.listingInfo}>
            <h1 className={styles.name}>{data.name}</h1>
            <div className={styles.identity}>
              <span
                className={`${
                  data.gender === "male"
                    ? styles.genderMale
                    : styles.genderFemale
                }`}
              >
                {data.gender === "male" && "Mle "}
                {data.gender === "female" && "Femelle "}
              </span>
              <span className={styles.age}>
                -{" "}
                <Moment locale="fr" fromNow ago>
                  {data.birth_date}
                </Moment>
              </span>
            </div>
            <span className={styles.city}>
              {data && data.city[0].toUpperCase() + data.city.substring(1)}
            </span>
          </div>
          <span className={styles.price}>{data.price} </span>
        </div>
        <div className={styles.listingBody}>
          <section className={styles.description}>
            <h2 className={styles.title}> propos de moi :</h2>
            <p>{data.description}</p>
          </section>
          <section className={styles.birthDate}>
            <h3>
              Je suis {data.gender === "male" ? "n" : "ne"} le
              <Moment className={styles.date} locale="fr" format="Do MMMM YYYY">
                {data.birth_date}
              </Moment>
              .
            </h3>
          </section>
          <section className={styles.about}>
            <h2 className={styles.title}>Mon caractre :</h2>
            <ul>
              <li>{data.about1}</li>
              <li>{data.about2}</li>
              <li>{data.about3}</li>
            </ul>
          </section>
          <section className={styles.health}>
            <h2 className={styles.title}>Ma sant :</h2>

            {data.category === "cat" && (
              <div className={styles.healthContainer}>
                <ul className={styles.column}>
                  <li>
                    Strilis{data.gender === "female" ? "e" : null} :
                    <span className={styles.answer}>
                      {data.sterilized ? "Oui" : "Non"}
                    </span>
                  </li>
                  <li>
                    Identifi{data.gender === "female" ? "e" : null} (puce ou
                    tatouage) :
                    <span className={styles.answer}>
                      {data.identified ? "Oui" : "Non"}
                    </span>
                  </li>
                  <li>
                    Vaccin{data.gender === "female" ? "e" : null} contre le
                    typhus :
                    <span
                      className={`${styles.answer} ${
                        data.typhus_vaccinated ? styles.good : styles.bad
                      }`}
                    >
                      {data.typhus_vaccinated ? "Oui" : "Non"}
                    </span>
                  </li>
                  <li>
                    Vaccin{data.gender === "female" ? "e" : null} contre le
                    coryza :
                    <span
                      className={`${styles.answer} ${
                        data.coryza_vaccinated ? styles.good : styles.bad
                      }`}
                    >
                      {data.coryza_vaccinated ? "Oui" : "Non"}
                    </span>
                  </li>
                  <li>
                    Vaccin{data.gender === "female" ? "e" : null} contre la
                    leucose :
                    <span
                      className={`${styles.answer} ${
                        data.felv_vaccinated ? styles.good : styles.bad
                      }`}
                    >
                      {data.felv_vaccinated ? "Oui" : "Non"}
                    </span>
                  </li>
                </ul>
                <ul className={styles.column}>
                  <li className={styles.test}>
                    Test FeLV :
                    <span
                      className={`${styles.answer} ${
                        data.felv_test === "+" ? styles.good : styles.bad
                      }`}
                    >
                      {data.felv_test}
                    </span>
                  </li>
                  <li className={styles.test}>
                    Test FiV :
                    <span
                      className={`${styles.answer} ${
                        data.fiv_test === "+" ? styles.good : styles.bad
                      }`}
                    >
                      {data.fiv_test}
                    </span>
                  </li>
                  <li>
                    Vaccin{data.gender === "female" ? "e" : null} contre la
                    rage :
                    <span
                      className={`${styles.answer} ${
                        data.rage_vaccinated ? styles.good : styles.bad
                      }`}
                    >
                      {data.rage_vaccinated ? "Oui" : "Non"}
                    </span>
                  </li>
                  <li>
                    Dparasit{data.gender === "female" ? "e" : null} :
                    <span
                      className={`${styles.answer} ${
                        data.dewormed ? styles.good : styles.bad
                      }`}
                    >
                      {data.dewormed ? "Oui" : "Non"}
                    </span>
                  </li>
                </ul>
              </div>
            )}

            {data.category === "dog" && (
              <div className={styles.healthContainer}>
                <ul className={styles.column}>
                  <li>
                    Strilis{data.gender === "female" ? "e" : null} :
                    <span className={styles.answer}>
                      {data.sterilized ? "Oui" : "Non"}
                    </span>
                  </li>
                  <li>
                    Identifi{data.gender === "female" ? "e" : null} (puce ou
                    tatouage) :
                    <span className={styles.answer}>
                      {data.identified ? "Oui" : "Non"}
                    </span>
                  </li>
                  <li>
                    Vaccin{data.gender === "female" ? "e" : null} contre la
                    maladie carre :
                    <span
                      className={`${styles.answer} ${
                        data.square_disease_vaccinated
                          ? styles.good
                          : styles.bad
                      }`}
                    >
                      {data.square_disease_vaccinated ? "Oui" : "Non"}
                    </span>
                  </li>
                  <li>
                    Vaccin{data.gender === "female" ? "e" : null} contre la
                    Parvovirose :
                    <span
                      className={`${styles.answer} ${
                        data.parvovirosis_vaccinated ? styles.good : styles.bad
                      }`}
                    >
                      {data.parvovirosis_vaccinated ? "Oui" : "Non"}
                    </span>
                  </li>
                </ul>
                <ul className={styles.column}>
                  <li>
                    Vaccin{data.gender === "female" ? "e" : null} contre
                    l'hpatite :
                    <span
                      className={`${styles.answer} ${
                        data.hepatitis_vaccinated ? styles.good : styles.bad
                      }`}
                    >
                      {data.hepatitis_vaccinated ? "Oui" : "Non"}
                    </span>
                  </li>
                  <li>
                    Vaccin{data.gender === "female" ? "e" : null} contre la
                    leptospirose :
                    <span
                      className={`${styles.answer} ${
                        data.leptovirosis_vaccinated ? styles.good : styles.bad
                      }`}
                    >
                      {data.leptovirosis_vaccinated ? "Oui" : "Non"}
                    </span>
                  </li>

                  <li>
                    Vaccin{data.gender === "female" ? "e" : null} contre la
                    rage :
                    <span
                      className={`${styles.answer} ${
                        data.rage_vaccinated ? styles.good : styles.bad
                      }`}
                    >
                      {data.rage_vaccinated ? "Oui" : "Non"}
                    </span>
                  </li>
                  <li>
                    Dparasit{data.gender === "female" ? "e" : null} :
                    <span
                      className={`${styles.answer} ${
                        data.dewormed ? styles.good : styles.bad
                      }`}
                    >
                      {data.dewormed ? "Oui" : "Non"}
                    </span>
                  </li>
                </ul>
              </div>
            )}
            {data.category === "rabbit" && (
              <div className={styles.healthContainer}>
                <ul className={styles.column}>
                  <li>
                    Strilis{data.gender === "female" ? "e" : null} :
                    <span className={styles.answer}>
                      {data.sterilized ? "Oui" : "Non"}
                    </span>
                  </li>

                  <li>
                    Vaccin{data.gender === "female" ? "e" : null} contre la
                    myxomatose :
                    <span
                      className={`${styles.answer} ${
                        data.myxomatosis_vaccinated ? styles.good : styles.bad
                      }`}
                    >
                      {data.myxomatosis_vaccinated ? "Oui" : "Non"}
                    </span>
                  </li>
                  <li>
                    Vaccin{data.gender === "female" ? "e" : null} contre le RH
                    0 :
                    <span
                      className={`${styles.answer} ${
                        data.rh_vaccinated ? styles.good : styles.bad
                      }`}
                    >
                      {data.rh_vaccinated ? "Oui" : "Non"}
                    </span>
                  </li>
                  <li>
                    Dparasit{data.gender === "female" ? "e" : null} :
                    <span
                      className={`${styles.answer} ${
                        data.dewormed ? styles.good : styles.bad
                      }`}
                    >
                      {data.dewormed ? "Oui" : "Non"}
                    </span>
                  </li>
                </ul>
              </div>
            )}
          </section>
        </div>
      </div>
      <div className={styles.menu}>
        <div className={styles.menuInfo}>
          <Link
            to={`/association/${data.association_id}`}
            className={styles.asso}
          >
            {associationData.name}
          </Link>

          <span>
            Annonce poste le
            <Moment className={styles.date} locale="fr" format="Do MMMM YYYY">
              {data.createdAt}
            </Moment>
            .
          </span>
        </div>
        <Link
          to={`/association/${data.association_id}`}
          className={styles.menuButton}
        >
          Contacter
        </Link>
      </div>
    </div>
  );
};

export default Listing;
