import styles from "./createListing.module.css";
import { ReactComponent as Male } from "./male.svg";
import { ReactComponent as Female } from "./female.svg";
import { ReactComponent as Plus } from "./plus.svg";
import { ReactComponent as Euro } from "./euro.svg";
import { useState, useEffect, useContext } from "react";

import { Formik, Form, Field } from "formik";
import * as Yup from "yup";

import TagInput from "../../../../shared/TagInput/TagInput";
import tagContext from "../../../../../contexts/tagContext";
import notificationContext from "./../../../../../contexts/notificationContext";

import api from "../../../../../services/api";

const CreateListing = () => {
  const [gender, setGender] = useState("");
  const [tags, setTags] = useState([]);
  const [error, setError] = useState(false);
  const [charNumber, setCharNumber] = useState(0);

  const { setNotification } = useContext(notificationContext);

  const [images, setImages] = useState([]);
  const [files, setFiles] = useState([null, null, null]);
  const [previews, setPreviews] = useState(["", "", ""]);
  const [filenames, setFilenames] = useState(["", "", ""]);

  let sentenceRegEx = /^([^0-9]*)$/i;

  const CreateListingSchema = Yup.object().shape({
    category: Yup.string().required("Slectionnez une catgorie"),
    name: Yup.string().required("Champ requis"),
    gender: Yup.string(),
    city: Yup.string()
      .matches(sentenceRegEx, "Ville non valide")
      .required("Champ requis"),
    birth_date: Yup.date("Date non valide").required("Champ requis"),
    image1: files[0]
      ? Yup.mixed()
          .test("fileSize", "Le fichier est trop lourd", (value) => {
            return value && files[0].size < 3000000;
          })
          .test("type", "Le format n'est pas valide", (value) => {
            return (
              value &&
              files[0] &&
              (files[0].type === "image/jpeg" ||
                files[0].type === "image/png" ||
                files[0].type === "image/gif" ||
                files[0].type === "image/webp")
            );
          })
          .required("Champ requis")
      : Yup.mixed().required("Champ requis"),
    image2: files[1]
      ? Yup.mixed()
          .test("fileSize", "Le fichier est trop lourd", (value) => {
            return value && files[1].size < 3000000;
          })
          .test("type", "Le format n'est pas valide", (value) => {
            return (
              value &&
              files[1] &&
              (files[1].type === "image/jpeg" ||
                files[1].type === "image/png" ||
                files[1].type === "image/gif" ||
                files[1].type === "image/webp")
            );
          })
          .required("Champ requis")
      : Yup.mixed().required("Champ requis"),
    image3: files[2]
      ? Yup.mixed()
          .test("fileSize", "Le fichier est trop lourd", (value) => {
            return value && files[2].size < 3000000;
          })
          .test("type", "Le format n'est pas valide", (value) => {
            return (
              value &&
              files[2] &&
              (files[2].type === "image/jpeg" ||
                files[2].type === "image/png" ||
                files[2].type === "image/gif" ||
                files[2].type === "image/webp")
            );
          })
          .required("Champ requis")
      : Yup.mixed().required("Champ requis"),
    description: Yup.string()
      .min(1, "Champ requis")
      .max(700, "La description est trop longue")
      .required("Champ requis"),
    price: Yup.number()
      .transform((_value, originalValue) =>
        Number(originalValue.replace(/,/, "."))
      )
      .max(999.99, "Montant non invalide")
      .min(0, "Montant non invalide")
      .required("Champ requis"),
    about1: Yup.string()
      .max(20, "Choisissez un adjectif plus court")
      .required("Champ requis"),
    about2: Yup.string().max(20, "Choisissez un adjectif plus court"),
    about3: Yup.string().max(20, "Choisissez un adjectif plus court"),
  });

  useEffect(() => {
    if (images[0]) {
      let reader = new FileReader();
      reader.onloadend = () => {
        setPreviews([reader.result, previews[1], previews[2]]);
      };
      reader.readAsDataURL(images[0]);
      setImages([]);
    }
    if (images[1]) {
      let reader2 = new FileReader();
      reader2.onloadend = () => {
        setPreviews([previews[0], reader2.result, previews[2]]);
      };
      reader2.readAsDataURL(images[1]);
      setImages([]);
    }
    if (images[2]) {
      let reader3 = new FileReader();
      reader3.onloadend = () => {
        setPreviews([previews[0], previews[1], reader3.result]);
      };
      reader3.readAsDataURL(images[2]);
      setImages([]);
    }
  }, [images, previews, files]);

  return (
    <div className={styles.contentPage}>
      <h1 className={styles.pageTitle}>Poster une annonce :</h1>
      <Formik
        initialValues={{
          name: "",
          gender: "male",
          category: "",
          price: "",
          city: "",
          image1: "",
          image2: "",
          image3: "",
          birth_date: "",
          description: "",
          // Common
          sterilized: "",
          identified: "",
          dewormed: "",
          rage_vaccinated: "",
          // Chats
          typhus_vaccinated: "",
          coryza_vaccinated: "",
          felv_test: "",
          fiv_test: "",
          felv_vaccinated: "",
          // Lapin
          myxomatosis_vaccinated: "",
          rh_vaccinated: "",
          // Chiens
          square_disease_vaccinated: "",
          parvovirose_vaccinated: "",
          hepatitis_vaccinated: "",
          leptospirosis_vaccinated: "",
          // End
          about1: "",
          about2: "",
          about3: "",
        }}
        validationSchema={CreateListingSchema}
        onSubmit={(values) => {
          values.about1 = tags[0];
          values.about2 = tags[1];
          values.about3 = tags[2];

          if (values.category === "cat") {
            // Lapins
            values.myxomatosis_vaccinated = "";
            values.rh_vaccinated = "";
            // Chiens
            values.square_disease_vaccinated = "";
            values.parvovirose_vaccinated = "";
            values.hepatitis_vaccinated = "";
            values.leptospirosis_vaccinated = "";
          }

          if (values.category === "dog") {
            values.typhus_vaccinated = "";
            values.coryza_vaccinated = "";
            values.felv_vaccinated = "";
            values.felv_test = "";
            values.fiv_test = "";
            // Lapins
            values.myxomatosis_vaccinated = "";
            values.rh_vaccinated = "";
          }
          if (!error) {
            let formData = new FormData();
            for (let value in values) {
              formData.append(value, values[value]);
            }
            setNotification({
              state: "shown",
              type: "loading",
              message: "Publication en cours...",
            });

            api
              .post("/create-listing", formData, {
                headers: {
                  "Content-Type": `multipart/form-data;`,
                },
              })
              .then(() => {
                setNotification({
                  state: "shown",
                  type: "success",
                  message: "Annonce publie avec succs",
                });
              });
          }
        }}
      >
        {({ errors, touched, values, setFieldValue, setFieldTouched }) => (
          <Form
            onKeyPress={(e) => {
              if (e.key === "Enter") {
                e.preventDefault();
              }
            }}
            onKeyDown={() => {
              setFieldValue("about1", tags[0]);
              setFieldValue("about2", tags[1]);
              setFieldValue("about3", tags[2]);
            }}
          >
            <div>
              <h2 className={styles.sectionTitle}>Informations :</h2>

              <div className={styles.formSection}>
                <label className={styles.label}>Catgorie :</label>
                <div
                  className={`select-container ${
                    errors.category && touched.category
                      ? "primary-input-error"
                      : "null"
                  }`}
                >
                  <Field className={styles.select} as="select" name="category">
                    <option value="" selected disabled>
                      Catgorie
                    </option>
                    <option value="dog">Chien</option>
                    <option value="cat">Chat</option>
                    <option value="rabbit">Lapin</option>
                    <option value="bird">Volatile</option>
                    <option value="rodent">Rongeur</option>
                  </Field>
                </div>
                {errors.category && touched.category ? (
                  <>
                    {setError(true)}
                    <div className="error-message">{errors.category}</div>
                  </>
                ) : null}
              </div>

              <div className={styles.formSection}>
                <label className={styles.label}>Nom :</label>
                <Field
                  className={`primary-input input-half ${
                    errors.name && touched.name ? "primary-input-error" : ""
                  }`}
                  type="text"
                  name="name"
                  placeholder="Nom"
                />
                {errors.name && touched.name ? (
                  <div className="error-message">{errors.name}</div>
                ) : null}
              </div>

              <div>
                <label className={styles.label}>Sexe :</label>
                <div className={styles.genderSelector}>
                  <div
                    className={`${styles.genderSelected} ${
                      gender === "female" ? styles.femaleSelected : ""
                    } ${gender === "male" ? styles.maleSelected : ""}`}
                  ></div>
                  <div className={styles.gender}>
                    <label htmlFor="male">
                      <div className={styles.genderSelection}>
                        <Male />
                      </div>
                    </label>
                    <Field
                      className={styles.input}
                      type="radio"
                      name="gender"
                      id="male"
                      value="male"
                      onClick={() => {
                        setGender("male");
                        setFieldValue("gender", "male");
                      }}
                      checked
                    />
                    <label htmlFor="male" className={styles.genderText}>
                      <span>Mle</span>
                    </label>
                  </div>
                  <div className={styles.gender}>
                    <label htmlFor="female">
                      <div className={styles.genderSelection}>
                        <Female />
                      </div>
                    </label>
                    <Field
                      className={styles.input}
                      type="radio"
                      name="gender"
                      id="female"
                      value="female"
                      onClick={() => {
                        setGender("female");
                        setFieldValue("gender", "female");
                      }}
                    />
                    <label htmlFor="female" className={styles.genderText}>
                      <span>Femelle</span>
                    </label>
                  </div>
                </div>
              </div>

              <div className={styles.formSection}>
                <label className={styles.label}>Frais d'adoption :</label>
                <div className={styles.priceContainer}>
                  <Field
                    className={`primary-input ${styles.input} ${
                      errors.price && touched.price ? "primary-input-error" : ""
                    }`}
                    placeholder="Frais d'adoption"
                    type="text"
                    name="price"
                  />
                  <Euro className={styles.euro} />
                </div>
                {errors.price && touched.price ? (
                  <div className="error-message">{errors.price}</div>
                ) : null}
              </div>

              <div className={styles.formSection}>
                <label className={styles.label}>Ville :</label>
                <Field
                  className={`primary-input input-half ${
                    errors.city && touched.city ? "primary-input-error" : ""
                  }`}
                  placeholder="Ville"
                  type="text"
                  name="city"
                />
                {errors.city && touched.city ? (
                  <div className="error-message">{errors.city}</div>
                ) : null}
              </div>

              <div className={styles.formSection}>
                <label className={styles.label}>Date de naissance :</label>
                <div className={styles.dateInputContainer}>
                  <Field
                    type="date"
                    name="birth_date"
                    className={styles.dateInput}
                  />
                </div>
              </div>
            </div>

            <div>
              <h2 className={styles.sectionTitle}>Images :</h2>
              <p className={styles.info}>
                Les images ne doivent pas faire plus de 3Mo.
              </p>
              <div className={styles.imageInputRow}>
                <div>
                  <label className={styles.label} htmlFor="image1">
                    <div className={styles.inputImg}>
                      {previews[0] ? (
                        <>
                          <img
                            src={previews[0]}
                            alt="image 1"
                            className={styles.imagePreview}
                          />
                          <span className={styles.modifyText}>Modifier</span>
                        </>
                      ) : (
                        <Plus className={styles.plus} />
                      )}
                    </div>
                  </label>
                  <input
                    className={styles.input}
                    type="file"
                    name="image1"
                    id="image1"
                    accept="image/png, image/gif, image/jpeg, image/webp"
                    onChange={(e) => {
                      const file = e.target.files[0];
                      if (file) {
                        setImages([file, images[1], images[2]]);
                        setFiles([file, files[1], files[2]]);
                        setFilenames([
                          e.target.value.split("\\").pop(),
                          filenames[1],
                          filenames[2],
                        ]);
                      }
                      setFieldValue("image1", e.target.files[0]);
                    }}
                  />
                  <span className={styles.filenames}>{filenames[0]}</span>
                  {errors.image1 && touched.image1 ? (
                    <div className="error-message">{errors.image1}</div>
                  ) : null}
                </div>
                <div>
                  <label className={styles.label} htmlFor="image2">
                    <div className={styles.inputImg}>
                      {previews[1] ? (
                        <>
                          <img
                            src={previews[1]}
                            alt="image 2"
                            className={styles.imagePreview}
                          />
                          <span className={styles.modifyText}>Modifier</span>
                        </>
                      ) : (
                        <Plus className={styles.plus} />
                      )}
                    </div>
                  </label>
                  <input
                    className={styles.input}
                    type="file"
                    name="image2"
                    id="image2"
                    accept="image/png, image/gif, image/jpeg, image/webp"
                    onChange={(e) => {
                      const file = e.target.files[0];
                      if (file) {
                        setImages([images[0], file, images[2]]);
                        setFiles([files[0], file, files[2]]);
                        setFilenames([
                          filenames[0],
                          e.target.value.split("\\").pop(),
                          filenames[2],
                        ]);
                      }
                      setFieldValue("image2", e.target.files[0]);
                    }}
                  />
                  <span className={styles.filenames}>{filenames[1]}</span>
                  {errors.image2 && touched.image2 ? (
                    <div className="error-message">{errors.image2}</div>
                  ) : null}
                </div>
                <div>
                  <label className={styles.label} htmlFor="image3">
                    <div className={styles.inputImg}>
                      {previews[2] ? (
                        <>
                          <img
                            src={previews[2]}
                            alt="image 3"
                            className={styles.imagePreview}
                          />
                          <span className={styles.modifyText}>Modifier</span>
                        </>
                      ) : (
                        <Plus className={styles.plus} />
                      )}
                    </div>
                  </label>
                  <input
                    className={styles.input}
                    type="file"
                    name="image3"
                    id="image3"
                    accept="image/png, image/gif, image/jpeg, image/webp"
                    onChange={(e) => {
                      const file = e.target.files[0];
                      if (file) {
                        setImages([images[0], images[1], file]);
                        setFiles([files[0], files[1], file]);
                        setFilenames([
                          filenames[0],
                          filenames[1],
                          e.target.value.split("\\").pop(),
                        ]);
                      }
                      setFieldValue("image3", e.target.files[0]);
                    }}
                  />
                  <span className={styles.filenames}>{filenames[2]}</span>
                  {errors.image3 && touched.image3 ? (
                    <div className="error-message">{errors.image3}</div>
                  ) : null}
                </div>
              </div>
            </div>

            <div>
              <h2 className={styles.sectionTitle}>A propos :</h2>
              <label className={styles.label}>Description :</label>
              <p className={styles.info}>600 caractres max</p>
              <div
                className={`${styles.textareaContainer} ${
                  errors.description && touched.description
                    ? "primary-input-error"
                    : ""
                }`}
              >
                <Field
                  className={styles.textarea}
                  as="textarea"
                  name="description"
                  placeholder="Description"
                  maxLength="600"
                  onKeyPress={(e) => e.stopPropagation()}
                  onChange={(e) => {
                    setCharNumber(e.target.value.length);
                    setFieldValue("description", e.target.value);
                  }}
                />

                <div className={styles.charCount}>
                  <span>{`${charNumber}/600`}</span>
                </div>
              </div>
              {errors.description && touched.description ? (
                <div className="error-message">{errors.description}</div>
              ) : null}
            </div>
            <div className={styles.formSection}>
              <label className={styles.label}>Caractre :</label>
              <p className={styles.info}>
                Pour valider un trait de caractre, appuyez sur entre.
              </p>
              <tagContext.Provider value={{ tags, setTags }}>
                <div
                  className={
                    touched.about1 &&
                    (errors.about1 || errors.about2 || errors.about3)
                      ? "primary-input-error"
                      : false
                  }
                >
                  <Field
                    name="about1"
                    setFieldTouched={setFieldTouched}
                    onKeyDown={(e) => {
                      setFieldValue("about1", tags[0]);
                      setFieldValue("about2", tags[1]);
                      setFieldValue("about3", tags[2]);
                    }}
                    component={TagInput}
                  />
                </div>
                {touched.about1 &&
                (errors.about1 || errors.about2 || errors.about3) ? (
                  <div className="error-message">{errors.about1}</div>
                ) : null}
              </tagContext.Provider>
            </div>
            <div className={styles.formSection}>
              <h2>Sant :</h2>
              {values.category === "" && (
                <p>Veuillez slectionner une catgorie</p>
              )}
              {values.category === "cat" && (
                <div className={styles.healthContainer}>
                  <ul className={styles.column}>
                    <li>
                      <Field
                        type="checkbox"
                        name="sterilized"
                        className={styles.healthCheckbox}
                      />
                      <label className={styles.label}>
                        Strilis{values.gender === "female" ? "e" : null}
                      </label>
                    </li>
                    <li>
                      <Field
                        type="checkbox"
                        name="identified"
                        className={styles.healthCheckbox}
                      />
                      <label className={styles.label}>
                        Identifi{values.gender === "female" ? "e" : null} (puce
                        ou tatouage)
                      </label>
                    </li>
                    <li>
                      <Field
                        type="checkbox"
                        name="typhus_vaccinated"
                        className={styles.healthCheckbox}
                      />
                      <label className={styles.label}>
                        Vaccin{values.gender === "female" ? "e" : null} contre
                        le typhus
                      </label>
                    </li>
                    <li>
                      <Field
                        type="checkbox"
                        name="coryza_vaccinated"
                        className={styles.healthCheckbox}
                      />
                      <label className={styles.label}>
                        Vaccin{values.gender === "female" ? "e" : null} contre
                        le coryza
                      </label>
                    </li>
                    <li>
                      <Field
                        type="checkbox"
                        name="felv_vaccinated"
                        className={styles.healthCheckbox}
                      />
                      <label className={styles.label}>
                        Vaccin{values.gender === "female" ? "e" : null} contre
                        la leucose
                      </label>
                    </li>
                  </ul>
                  <ul className={styles.column}>
                    <li className={styles.test}>
                      <label className={`${styles.label} ${styles.testLabel}`}>
                        Test FeLV :
                      </label>
                      <div
                        className={`select-container half ${styles.testSelect}`}
                      >
                        <Field
                          className={styles.select}
                          as="select"
                          name="felv_test"
                        >
                          <option value="" selected disabled>
                            + ou -
                          </option>
                          <option value="+">+</option>
                          <option value="-">-</option>
                        </Field>
                      </div>
                    </li>
                    <li className={styles.test}>
                      <label className={`${styles.label} ${styles.testLabel}`}>
                        Test FiV :
                      </label>
                      <div
                        className={`select-container half ${styles.testSelect}`}
                      >
                        <Field
                          className={styles.select}
                          as="select"
                          name="fiv_test"
                        >
                          <option value="" selected disabled>
                            + ou -
                          </option>
                          <option value="+">+</option>
                          <option value="-">-</option>
                        </Field>
                      </div>
                    </li>
                    <li>
                      <Field
                        type="checkbox"
                        name="rage_vaccinated"
                        className={styles.healthCheckbox}
                      />
                      <label className={styles.label}>
                        Vaccin{values.gender === "female" ? "e" : null} contre
                        la rage
                      </label>
                    </li>
                    <li>
                      <Field
                        type="checkbox"
                        name="dewormed"
                        className={styles.healthCheckbox}
                      />
                      <label className={styles.label}>
                        Dparasit{values.gender === "female" ? "e" : null}
                      </label>
                    </li>
                  </ul>
                </div>
              )}
              {values.category === "dog" && (
                <div className={styles.healthContainer}>
                  <ul className={styles.column}>
                    <li>
                      <Field
                        type="checkbox"
                        name="sterilized"
                        className={styles.healthCheckbox}
                      />
                      <label className={styles.label}>
                        Strilis{values.gender === "female" ? "e" : null}
                      </label>
                    </li>
                    <li>
                      <Field
                        type="checkbox"
                        name="identified"
                        className={styles.healthCheckbox}
                      />
                      <label className={styles.label}>
                        Identifi{values.gender === "female" ? "e" : null}
                        (puce ou tatouage)
                      </label>
                    </li>
                    <li>
                      <Field
                        type="checkbox"
                        name="square_disease_vaccinated"
                        className={styles.healthCheckbox}
                      />
                      <label className={styles.label}>
                        Vaccin{values.gender === "female" ? "e" : null} contre
                        la maladie carre
                      </label>
                    </li>
                    <li>
                      <Field
                        type="checkbox"
                        name="parvovirosis_vaccinated"
                        className={styles.healthCheckbox}
                      />
                      <label className={styles.label}>
                        Vaccin{values.gender === "female" ? "e" : null} contre
                        la Parvovirose
                      </label>
                    </li>
                  </ul>
                  <ul className={styles.column}>
                    <li>
                      <Field
                        type="checkbox"
                        name="hepatitis_vaccinated"
                        className={styles.healthCheckbox}
                      />
                      <label className={styles.label}>
                        Vaccin{values.gender === "female" ? "e" : null} contre
                        l'hpatite
                      </label>
                    </li>
                    <li>
                      <Field
                        type="checkbox"
                        name="leptospirosis_vaccinated"
                        className={styles.healthCheckbox}
                      />
                      <label className={styles.label}>
                        Vaccin{values.gender === "female" ? "e" : null} contre
                        la leptospirose
                      </label>
                    </li>

                    <li>
                      <Field
                        type="checkbox"
                        name="rage_vaccinated"
                        className={styles.healthCheckbox}
                      />
                      <label className={styles.label}>
                        Vaccin{values.gender === "female" ? "e" : null} contre
                        la rage
                      </label>
                    </li>
                    <li>
                      <Field
                        type="checkbox"
                        name="dewormed"
                        className={styles.healthCheckbox}
                      />
                      <label className={styles.label}>
                        Dparasit{values.gender === "female" ? "e" : null}
                      </label>
                    </li>
                  </ul>
                </div>
              )}
              {values.category === "rabbit" && (
                <div className={styles.healthContainer}>
                  <ul className={styles.column}>
                    <li>
                      <Field
                        type="checkbox"
                        name="sterilized"
                        className={styles.healthCheckbox}
                      />
                      <label className={styles.label}>
                        Strilis{values.gender === "female" ? "e" : null}
                      </label>
                    </li>

                    <li>
                      <Field
                        type="checkbox"
                        name="myxomatosis_vaccinated"
                        className={styles.healthCheckbox}
                      />
                      <label className={styles.label}>
                        Vaccin{values.gender === "female" ? "e" : null} contre
                        la myxomatose
                      </label>
                    </li>
                    <li>
                      <Field
                        type="checkbox"
                        name="rh_vaccinated"
                        className={styles.healthCheckbox}
                      />
                      <label className={styles.label}>
                        Vaccin{values.gender === "female" ? "e" : null} contre
                        le RH 0
                      </label>
                    </li>
                    <li>
                      <Field
                        type="checkbox"
                        name="dewormed"
                        className={styles.healthCheckbox}
                      />
                      <label className={styles.label}>
                        Dparasit{values.gender === "female" ? "e" : null}
                      </label>
                    </li>
                  </ul>
                </div>
              )}
            </div>
            <div className={styles.buttons}>
              <button className="primary-button" type="submit">
                Poster l'annonce
              </button>
            </div>
          </Form>
        )}
      </Formik>
    </div>
  );
};

export default CreateListing;
