import { useState, useContext } from "react";
import styles from "./TagInput.module.css";

import { ReactComponent as Cross } from "./remove.svg";
import tagContext from "../../../contexts/tagContext";

const TagInput = () => {
  const [inputValue, setInputValue] = useState("");
  const { tags, setTags } = useContext(tagContext);

  const handleKeyPress = (event) => {
    if (
      (event.key === "Backspace" && inputValue.length === 0) ||
      (event.key === "Delete" && inputValue.length === 0)
    ) {
      if (tags.length === 1) {
        setTags([]);
      }
      if (tags.length === 2) {
        setTags([tags[0]]);
      }
      if (tags.length === 3) {
        setTags([tags[0], tags[1]]);
      }
    }

    if (event.key === "Enter" && inputValue.length > 0) {
      if (tags.length === 0) {
        setTags([inputValue]);
      }
      if (tags.length === 1) {
        setTags([tags[0], inputValue]);
      }
      if (tags.length === 2) {
        setTags([tags[0], tags[1], inputValue]);
      }

      setInputValue("");
    }
  };

  return (
    <>
      <div
        className={`${styles.tagInputContainer} ${
          tags.length > 0 ? styles.filled : ""
        }`}
      >
        {tags.map((tag) => {
          return (
            <div key={tag} className={styles.tag}>
              <span className={styles.tagValue}>{tag}</span>
              <Cross
                className={styles.cross}
                onClick={() =>
                  setTags(tags.filter((element) => element !== tag))
                }
              />
            </div>
          );
        })}
        {!tags[2] && (
          <input
            className={styles.input}
            type="text"
            value={inputValue}
            placeholder={`${tags.length === 0 ? "Traits (3)" : ""}${
              tags.length === 1 ? "Traits (2)" : ""
            }${tags.length === 2 ? "Traits (1)" : ""}`}
            onKeyDown={handleKeyPress}
            onChange={(e) => setInputValue(e.target.value)}
          />
        )}
      </div>
    </>
  );
};

export default TagInput;
