import { useContext, useEffect } from "react";
import userContext from "../../../../contexts/userContext";
import notificationContext from "../../../../contexts/notificationContext";
import authContext from "../../../../contexts/authContext";
import { deletePopUpContext } from "./../../../../contexts/shared/deletePopUpContext";
import { currentPageContext } from "./../../../../contexts/shared/currentPageContext";
import styles from "./Account.module.css";

import { withRouter } from "react-router-dom";
import { Formik, Form, Field } from "formik";
import * as Yup from "yup";

import defaultAccount from "./Default.svg";

import Cookies from "js-cookie";
import api from "./../../../../services/api";

const Account = () => {
  const { user, setUser } = useContext(userContext);
  const { setIsAuthenticated } = useContext(authContext);
  const { setNotification } = useContext(notificationContext);
  const { setDeletePopUp } = useContext(deletePopUpContext);
  const { setCurrentPage } = useContext(currentPageContext);

  let cityRegEx = /^([^0-9]*)$/i;

  let citySchema = Yup.object().shape({
    city: Yup.string()
      .matches(cityRegEx, "Ville invalide")
      .required("Champ requis"),
  });
  let emailSchema = Yup.object().shape({
    email: Yup.string()
      .email("Email invalide")
      .oneOf(
        [Yup.ref("confirmEmail"), null],
        "Les adresses mail ne correspondent pas"
      )
      .required("Champ requis"),
    confirmEmail: Yup.string().email().required("Champ requis"),
  });

  let passwordRegEx = /^(?=.*\d)(?=.*[a-z])(?=.*[A-Z]).{8,}$/;

  let passwordSchema = Yup.object().shape({
    password: Yup.string()
      .min(8, "Le mot de passe doit faire au moins 8 caractres")
      .matches(
        passwordRegEx,
        "Le mot de passe doit contenir au moins une majuscule, une minuscule et un chiffre"
      )
      .required("Tous les champs sont requis"),
    newPassword: Yup.string()
      .oneOf(
        [Yup.ref("confirmNewPassword"), null],
        "Les mots de passe ne correspondent pas"
      )
      .required("Tous les champs sont requis"),
    confirmNewPassword: Yup.string().required("Tous les champs sont requis"),
  });

  useEffect(() => {
    setCurrentPage("");
    api.get("/user").then((res) => {
      setUser(res.data);
    });
  }, []);

  const handleLogout = () => {
    api
      .post("/logout")
      .then((res) => {
        if (res) {
          Cookies.remove("x-access-token");
          Cookies.remove("x-refresh-token");
          Cookies.remove("authenticated");
          Cookies.remove("hasAssociation");
          Cookies.remove("association_uuid");
          Cookies.remove("association_id");
          setIsAuthenticated(false);
          setUser(null);
          setNotification({
            state: "shown",
            type: "success",
            message: "Vous avez t dconnect",
          });
        }
      })
      .catch((error) => {
        if (error) {
          setNotification({
            state: "shown",
            type: "error",
            message: error.response.data.error,
          });
        }
      });
  };

  const handleDelete = () => {
    setDeletePopUp({
      isShown: true,
      toDelete: "user",
      title: "Supprimer le compte",
      text: 'Pour confirmer la suppression de votre compte, veuillez crire "Supprimer dfinitivement".\nSi votre compte est rattach  une association, celle-ci ainsi que toutes ses annonces seront galement supprimes.',
      textToEnter: "Supprimer dfinitivement",
      link: "/user/delete",
      buttonText: "Supprimer mon compte",
    });
  };

  return (
    <div className={styles.accountContainer}>
      <h1 className={styles.title}>Votre compte :</h1>
      <div className={styles.accountCard}>
        <div className={styles.accountHeader}>
          <img
            className={styles.profileImg}
            src={defaultAccount}
            alt="profile"
          />
          <div className={styles.accountHeaderText}>
            <div>
              <h2 className={styles.name}>{user.firstname}</h2>
              <h2 className={styles.name}>{user.lastname}</h2>
            </div>
            {user.city ? (
              <div>
                <h3 className={styles.departement}>
                  {user.city.charAt(0).toUpperCase() + user.city.slice(1)}
                </h3>
              </div>
            ) : (
              ""
            )}
          </div>
        </div>
        <div className={styles.accountBody}>
          <section className={styles.accountSection}>
            <h2>Vos informations :</h2>
            <div className={styles.identity}>
              <div className={styles.info}>
                <label className={styles.label}>Nom complet :</label>
                <p
                  className={styles.data}
                >{`${user.firstname} ${user.lastname}`}</p>
              </div>
              <div className={styles.info}>
                <label className={styles.label}>Ville :</label>
                <p className={styles.data}>
                  {user.city
                    ? user.city.charAt(0).toUpperCase() + user.city.slice(1)
                    : "Inconnue"}
                </p>
              </div>
              <div className={styles.info}>
                <label className={styles.label}>Email :</label>
                <p className={styles.data}>{user.email}</p>
              </div>
            </div>
          </section>
          <section className={styles.accountSection}>
            <Formik
              initialValues={{
                city: user.city
                  ? user.city.charAt(0).toUpperCase() + user.city.slice(1)
                  : "",
              }}
              validationSchema={citySchema}
              validateOnChange={false}
              validateOnBlur={false}
              enableReinitialize
              onSubmit={(values) => {
                setNotification({
                  type: "loading",
                  state: "shown",
                  message: "Chargement en cours...",
                });
                api
                  .put(`/user/update/city`, values)
                  .then((res) => {
                    setNotification({
                      type: "success",
                      state: "shown",
                      message: "Ville mis  jour",
                    });
                  })
                  .catch((err) => {
                    setNotification({
                      type: "error",
                      state: "shown",
                      message: err.response.message,
                    });
                  });
              }}
            >
              {({ errors }) => (
                <Form>
                  <h2>Modifier vos informations :</h2>
                  <div className={styles.modifyInfo}>
                    <label className={styles.label}>Ville :</label>
                    <div
                      className={`row-space-between align-center ${styles.row}`}
                    >
                      <Field
                        type="text"
                        name="city"
                        placeholder="Ville"
                        className={`${styles.input} ${styles.cityInput} ${
                          errors.city ? "secondary-input-error" : ""
                        }`}
                        required
                      />

                      <button className="primary-button" type="submit">
                        Modifier
                      </button>
                    </div>
                    {errors.city ? (
                      <div className="error-message">{errors.city}</div>
                    ) : null}
                  </div>
                </Form>
              )}
            </Formik>
            <div className={styles.modifyInfo}>
              <Formik
                initialValues={{ email: "", confirmEmail: "" }}
                validationSchema={emailSchema}
                validateOnChange={false}
                validateOnBlur={false}
                onSubmit={(values) => {
                  let confirm = window.confirm(
                    "tes vous sr de vouloir changer l'email de votre compte ?"
                  );

                  if (confirm) {
                    setNotification({
                      type: "loading",
                      state: "shown",
                      message: "Chargement en cours...",
                    });
                    api
                      .put(`/user/update/email`, values)
                      .then((res) => {
                        setNotification({
                          type: "success",
                          state: "shown",
                          message: "Email mis  jour",
                        });
                      })
                      .catch((err) => {
                        setNotification({
                          type: "error",
                          state: "shown",
                          message: err.response.message,
                        });
                      });
                  }
                }}
              >
                {({ errors }) => (
                  <Form>
                    <label className={styles.label}>Email :</label>
                    <Field
                      className={`${styles.input} ${
                        errors.email ? "secondary-input-error" : ""
                      }`}
                      type="email"
                      name="email"
                      placeholder="Email"
                    />
                    <label className={styles.label}>Confirmer email :</label>
                    <Field
                      className={`${styles.input} ${
                        errors.email ? "secondary-input-error" : ""
                      }`}
                      type="email"
                      name="confirmEmail"
                      placeholder="Confirmer email"
                    />
                    {errors.email && (
                      <div className="error-message">{errors.email}</div>
                    )}
                    <button
                      className={`primary-button ${styles.button}`}
                      type="submit"
                    >
                      Changer email
                    </button>
                  </Form>
                )}
              </Formik>
            </div>
            <div className={styles.modifyInfo}>
              <Formik
                initialValues={{
                  password: "",
                  newPassword: "",
                  confirmNewPassword: "",
                }}
                validationSchema={passwordSchema}
                validateOnChange={false}
                validateOnBlur={false}
                onSubmit={(values) => {
                  setNotification({
                    type: "loading",
                    state: "shown",
                    message: "Chargement en cours...",
                  });
                  api
                    .put(`/user/update/password`, values)
                    .then((res) => {
                      setNotification({
                        type: "success",
                        state: "shown",
                        message: "Mot de passe mis  jour",
                      });
                    })
                    .catch((err) => {
                      setNotification({
                        type: "error",
                        state: "shown",
                        message: err.response.message,
                      });
                    });
                }}
              >
                {({ errors }) => (
                  <Form>
                    <label className={styles.label}>
                      Mot de passe actuel :
                    </label>
                    <Field
                      className={styles.input}
                      type="password"
                      name="password"
                      placeholder="Mot de passe actuel"
                    />
                    <label className={styles.label}>
                      Nouveau mot de passe :
                    </label>
                    <Field
                      className={`${styles.input} ${
                        errors.newPassword ? "secondary-input-error" : ""
                      }`}
                      type="password"
                      name="newPassword"
                      placeholder="Nouveau mot de passe"
                    />
                    <label className={styles.label}>
                      Confirmer nouveau mot de passe :
                    </label>
                    <Field
                      className={`${styles.input} ${
                        errors.newPassword ? "secondary-input-error" : ""
                      }`}
                      type="password"
                      name="confirmNewPassword"
                      placeholder="Confirmer nouveau mot de passe"
                    />
                    {errors.newPassword && (
                      <div className="error-message">{errors.newPassword}</div>
                    )}
                    <button
                      type="submit"
                      className={`primary-button ${styles.button}`}
                    >
                      Changer mot de passe
                    </button>
                  </Form>
                )}
              </Formik>
            </div>
          </section>
          <section>
            <h2>Actions sur votre compte :</h2>
            <div className={styles.accountActions}>
              <button className="primary-button" onClick={handleLogout}>
                Se dconnecter
              </button>
              <button
                onClick={handleDelete}
                className={`red-outlined-button ${styles.deleteAccount}`}
              >
                Supprimer le compte
              </button>
            </div>
          </section>
        </div>
      </div>
    </div>
  );
};

export default withRouter(Account);
