import styles from "./PanelNav.module.css";

import { ReactComponent as DashboardIcon } from "./Dashboard.svg";
import { ReactComponent as ListingsIcon } from "./Listings.svg";
import { ReactComponent as ProfileIcon } from "./associationProfile.svg";
import { ReactComponent as SettingsIcon } from "./settings.svg";
import { ReactComponent as QuestionIcon } from "./question.svg";
import { ReactComponent as Wrapped } from "./wrapped.svg";
import { ReactComponent as Unwrapped } from "./unwrapped.svg";

import { Link } from "react-router-dom";
import { useState, useEffect } from "react";
import { useMediaQuery } from "react-responsive";

const PanelNav = () => {
  const [isWrapped, setIsWrapped] = useState(false);
  const hasToWrap = useMediaQuery({ query: `(max-width: 800px)` });
  useEffect(() => {
    if (hasToWrap) {
      setIsWrapped(true);
    }
  }, [hasToWrap]);

  return (
    <nav className={`${styles.nav} ${isWrapped ? styles.wrapped : ""}`}>
      <div className={styles.navTop}>
        <div
          className={`${styles.navLink} ${styles.wrapBtn}`}
          onClick={() => !hasToWrap && setIsWrapped(!isWrapped)}
        >
          <Wrapped
            className={`${styles.wrapIcon} ${
              isWrapped ? styles.visible : styles.hidden
            }`}
          />
          <Unwrapped
            className={`${styles.wrapIcon} ${
              isWrapped ? styles.hidden : styles.vidible
            }`}
          />
        </div>
        <Link to="/associations/panel/dashboard" className={styles.navLink}>
          <DashboardIcon />
          <span className={styles.navText}>Tableau de bord</span>
        </Link>
        <Link to="/associations/panel/listings" className={styles.navLink}>
          <ListingsIcon />
          <span className={styles.navText}>Annonces</span>
        </Link>
        <Link to="/associations/panel/profile" className={styles.navLink}>
          <ProfileIcon />
          <span className={styles.navText}>Profil public</span>
        </Link>
      </div>

      <div className={styles.navBot}>
        <Link className={styles.navLink} to="/associations/panel/settings">
          <SettingsIcon />
          <span className={styles.navText}>Paramtres</span>
        </Link>
        <Link className={styles.navLink} to="/associations/panel/help">
          <QuestionIcon />
          <span className={styles.navText}>Aide</span>
        </Link>
      </div>
    </nav>
  );
};

export default PanelNav;
