import { useState, useEffect, useContext } from "react";
import { withRouter, Link, Redirect } from "react-router-dom";

import styles from "./CreateAssociation.module.css";

import { Formik, Form, Field } from "formik";
import * as Yup from "yup";
import Cookies from "js-cookie";

import { ReactComponent as Arrow } from "./arrow.svg";

import { AssociationContext } from "../../../../contexts/associations/assoContext";
import NotificationsContext from "../../../../contexts/notificationContext";
import api from "./../../../../services/api";

const CreateAssociation = () => {
  const [step, setStep] = useState(0);
  const [numberError, setNumberError] = useState(false);

  const { setAssociation } = useContext(AssociationContext);
  const { setNotification } = useContext(NotificationsContext);

  const [formValues, setFormValues] = useState({
    civilite: "",
    lastname: "",
    firstname: "",
    email: "",
    phone: "",
    address: "",
    city: "",
    postCode: "",
    assoName: "",
    assoObject: "",
    assoAddress: "",
    assoCity: "",
    assoPostCode: "",
    assoNumber: "",
    assoSiretNumber: "",
    authEmail: "",
    authPassword: "",
  });

  const [numberType, setNumberType] = useState("siret");
  const [isErrors, setIsErrors] = useState(false);

  let addressRegEx = /^[0-9]{1,3}(?:(?:[,. ]){1}[-a-zA-Z]+)+$/i;
  let phoneRegEx =
    /^(?:(?:\+|00)33[\s.-]{0,3}(?:\(0\)[\s.-]{0,3})?|0)[1-9](?:(?:[\s.-]?\d{2}){4}|\d{2}(?:[\s.-]?\d{3}){2})$/i;

  let postCodeRegEx = /^(([0-8][0-9])|(9[0-5])|(2[ab]))[0-9]{3}$/i;
  let sentenceRegEx = /^([^0-9]*)$/i;

  const secondSchema = Yup.object().shape({
    civilite: Yup.string().required("Champ Requis"),
    lastname: Yup.string("Nom invalide")
      .matches(sentenceRegEx, "Nom invalide")
      .required("Champ requis"),
    firstname: Yup.string("Prnom invalide")
      .matches(sentenceRegEx, "Prnom invalide")
      .required("Champ requis"),
    email: Yup.string().email("Email invalide").required("Champ Requis"),
    phone: Yup.string()
      .matches(phoneRegEx, "Numro non valide")
      .required("Champ requis"),
    address: Yup.string()
      .matches(addressRegEx, "Adresse non valide")
      .required("Champ requis"),
    city: Yup.string()
      .matches(sentenceRegEx, "Ville non valide")
      .required("Champ requis"),
    postCode: Yup.string()
      .matches(postCodeRegEx, "Code Postal non valide")
      .required("Champ requis"),
  });

  const thirdSchema = Yup.object().shape({
    assoName: Yup.string("Nom invalide").required("Champ requis"),
    assoObject: Yup.string("Objet non valide")
      .max(400, "Objet trop long")
      .required("Champ requis"),
    assoAddress: Yup.string()
      .matches(addressRegEx, "Adresse non valide")
      .required("Champ requis"),
    assoCity: Yup.string()
      .matches(sentenceRegEx, "Ville non valide")
      .required("Champ requis"),
    assoPostCode: Yup.string()
      .matches(postCodeRegEx, "Code postal non valide")
      .required("Champ requis"),
  });

  const fourthSchema = Yup.object().shape({
    assoSiretNumber:
      numberType === "siret"
        ? Yup.string()
            .min(14, "Numro non valide")
            .max(14, "Numro non valide")
            .required("Champ Requis")
        : Yup.string()
            .min(14, "Numro non valide")
            .max(14, "Numro non valide"),
    assoNumber:
      numberType === "association"
        ? Yup.string()
            .min(5, "Numro non valide")
            .max(100, "Numro non valide")
            .required("Champ requis")
        : Yup.string()
            .min(5, "Numro non valide")
            .max(100, "Numro non valide"),
  });

  useEffect(() => {
    if (!isErrors && !numberError && step === 4) {
      api
        .post("/associations/create", formValues)
        .then(async (res) => {
          const {
            hasAssociation,
            association_uuid,
            association_id,
            accessToken,
            refreshToken,
          } = res.data;

          Cookies.set("x-access-token", accessToken, {
            //secure: true
          });
          Cookies.set("x-refresh-token", refreshToken, {
            //secure: true
          });
          Cookies.set("hasAssociation", hasAssociation, {
            //secure: true
          });
          Cookies.set("association_uuid", association_uuid, {
            //secure: true
          });
          Cookies.set("association_id", association_id, {
            //secure: true
          });
        })
        .then(async () => {
          api.get(`/associations/public/`).then((res) => {
            setAssociation(res.data);
          });
        })
        .catch((err) => {
          setNotification({
            type: "error",
            state: "shown",
            message: err.response.message,
          });
        });
    }
  }, [formValues, isErrors, step]);

  const hasAssociation = Cookies.get("hasAssociation");
  if (hasAssociation === "true" && step !== 4) {
    return <Redirect to="/associations/panel/dashboard" />;
  } else {
    return (
      <div
        className={`${styles.container} ${
          step === 0 || step === 3 || step === 4 ? styles.absoluteContainer : ""
        }`}
      >
        <div
          className={`${styles.firstStep} ${
            step !== 0 ? styles.hiddenStep : ""
          }`}
        >
          <h1>Vous dtenez une association animalire ?</h1>
          <p className={styles.firstStepDescription}>
            Rejoignez-nous ! Postez vos annonces sur AnimaZone et augmentez la
            visibilit de votre association.
          </p>
          <button
            onClick={() => setStep(1)}
            className={`primary-button ${styles.nextButton}`}
          >
            <span>Commencer</span>
            <Arrow className={styles.nextArrow} />
          </button>
        </div>

        <Formik
          initialValues={{
            civilite: "",
            lastname: "",
            firstname: "",
            email: "",
            phone: "",
            address: "",
            city: "",
            postCode: "",
          }}
          validationSchema={secondSchema}
          onSubmit={async (values) => {
            setFormValues({
              ...formValues,
              civilite: values.civilite,
              lastname: values.lastname,
              firstname: values.firstname,
              email: values.email,
              phone: values.phone,
              address: values.address,
              city: values.city,
              postCode: values.postCode,
            });

            isErrors ? alert("Veuillez complter le formulaire") : setStep(2);
            setIsErrors(false);
          }}
        >
          {({ errors, touched }) => (
            <Form>
              <div
                className={`${styles.step} ${
                  step !== 1 ? styles.hiddenStep : ""
                }`}
              >
                <h1 className={styles.title}>Faisons connaissance !</h1>
                <h2 className={styles.sectionTitle}>
                  Renseignez les informations du{" "}
                  <span className="underline"> reprsentant lgal</span> de
                  l'association :
                </h2>
                <p className={styles.description}>
                  Tous les champs sont obligatoires
                </p>
                <div className={styles.formSection}>
                  <label className={styles.label}>Civilit :</label>
                  <div className="row">
                    <div className={styles.radio}>
                      <Field type="radio" name="civilite" id="M" value="M" />
                      <label htmlFor="M">M</label>
                    </div>

                    <div className={styles.radio}>
                      <Field
                        type="radio"
                        name="civilite"
                        id="Mme"
                        value="Mme"
                      />
                      <label htmlFor="Mme">Mme</label>
                    </div>

                    <div className={styles.radio}>
                      <Field
                        type="radio"
                        name="civilite"
                        id="Mlle"
                        value="Mlle"
                      />
                      <label htmlFor="Mlle">Mlle</label>
                    </div>
                  </div>
                  {errors.civilite && touched.civilite ? (
                    <div className={styles.errorMessage}>{errors.civilite}</div>
                  ) : null}
                </div>
                <div className="row-space-between align-flex-start">
                  <div className={`${styles.formSection} half-section`}>
                    <label className={styles.label}>Nom :</label>
                    <Field
                      type="text"
                      className={`${styles.input} ${
                        errors.lastname && touched.lastname
                          ? styles.inputError
                          : ""
                      }`}
                      name="lastname"
                      placeholder="Nom"
                    />
                    {errors.lastname && touched.lastname ? (
                      <div className={styles.errorMessage}>
                        {errors.lastname}
                      </div>
                    ) : null}
                  </div>
                  <div className={`${styles.formSection} half-section`}>
                    <label className={styles.label}>Prnom :</label>
                    <Field
                      type="text"
                      className={`${styles.input} ${
                        errors.firstname && touched.firstname
                          ? styles.inputError
                          : ""
                      }`}
                      name="firstname"
                      placeholder="Prnom"
                    />
                    {errors.firstname && touched.firstname ? (
                      <div className={styles.errorMessage}>
                        {errors.firstname}
                      </div>
                    ) : null}
                  </div>
                </div>

                <div className={styles.formSection}>
                  <label className={styles.label}>Adresse Mail :</label>
                  <Field
                    type="email"
                    className={`${styles.input} ${
                      errors.email && touched.email ? styles.inputError : ""
                    }`}
                    name="email"
                    placeholder="Email"
                  />
                  {errors.email && touched.email ? (
                    <div className={styles.errorMessage}>{errors.email}</div>
                  ) : null}
                </div>
                <div className={styles.formSection}>
                  <label className={styles.label}>Tlphone :</label>
                  <Field
                    className={`${styles.input} ${
                      errors.phone && touched.phone ? styles.inputError : ""
                    }`}
                    type="tel"
                    name="phone"
                    placeholder="N de tlphone"
                  />
                  {errors.phone && touched.phone ? (
                    <div className={styles.errorMessage}>{errors.phone}</div>
                  ) : null}
                </div>
                <div className={styles.formSection}>
                  <label className={styles.label}>Adresse :</label>
                  <Field
                    className={`${styles.input} ${
                      errors.address && touched.address ? styles.inputError : ""
                    }`}
                    type="text"
                    name="address"
                    placeholder="N de voie et Rue"
                  />
                  {errors.address && touched.address ? (
                    <div className={styles.errorMessage}>{errors.address}</div>
                  ) : null}
                </div>

                <div className="row-space-between align-flex-start">
                  <div
                    className={`${styles.formSection} two-third ${styles.cityInput}`}
                  >
                    <label className={styles.label}>Ville :</label>
                    <Field
                      className={`${styles.input} ${
                        errors.city && touched.city ? styles.inputError : ""
                      }`}
                      type="text"
                      name="city"
                      placeholder="Ville"
                    />
                    {errors.city && touched.city ? (
                      <div className={styles.errorMessage}>{errors.city}</div>
                    ) : null}
                  </div>
                  <div
                    className={`${styles.formSection} one-third ${styles.postCode}`}
                  >
                    <label className={styles.label}>Code postal :</label>
                    <Field
                      className={`${styles.input} ${
                        errors.postCode && touched.postCode
                          ? styles.inputError
                          : ""
                      }`}
                      type="text"
                      name="postCode"
                      placeholder="Code postal"
                    />
                    {errors.postCode && touched.postCode ? (
                      <div className={styles.errorMessage}>
                        {errors.postCode}
                      </div>
                    ) : null}
                  </div>
                </div>

                <div className={`${styles.buttons} row-space-between`}>
                  <button
                    onClick={() => setStep(0)}
                    className={`outlined-button ${styles.previousButton}`}
                  >
                    <Arrow className={styles.previousArrow} />
                    <span>Prcdent</span>
                  </button>
                  <button
                    type="submit"
                    onClick={() => {
                      if (Object.keys(errors).length !== 0) {
                        setIsErrors(true);
                      } else {
                        setIsErrors(false);
                      }
                    }}
                    className="primary-button"
                  >
                    <span>Suivant</span>
                    <Arrow className={styles.nextArrow} />
                  </button>
                </div>
              </div>
            </Form>
          )}
        </Formik>

        <Formik
          initialValues={{
            assoName: "",
            assoObject: "",
            assoAddress: "",
            assoCity: "",
            assoPostCode: "",
          }}
          validationSchema={thirdSchema}
          onSubmit={(values) => {
            setFormValues({
              ...formValues,
              assoName: values.assoName,
              assoObject: values.assoObject,
              assoAddress: values.assoAddress,
              assoCity: values.assoCity,
              assoPostCode: values.assoPostCode,
            });
            isErrors ? alert("Veuillez complter le formulaire") : setStep(3);
            setIsErrors(false);
          }}
        >
          {({ errors, touched }) => (
            <Form>
              <div
                className={`${styles.step} ${
                  step !== 2 ? styles.hiddenStep : ""
                }`}
              >
                <h1 className={styles.title}>Votre association :</h1>

                <p className={styles.description}>
                  Tous les champs sont obligatoires
                </p>
                <div className={styles.formSection}>
                  <label className={styles.label}>Nom :</label>
                  <Field
                    type="text"
                    className={`${styles.input} ${
                      errors.assoName && touched.assoName
                        ? styles.inputError
                        : ""
                    }`}
                    name="assoName"
                    placeholder="Nom de l'association"
                  />
                  {errors.assoName && touched.assoName ? (
                    <div className={styles.errorMessage}>{errors.assoName}</div>
                  ) : null}
                </div>
                <div className={styles.formSection}>
                  <label className={styles.label}>
                    Objet de l'association :
                  </label>
                  <Field
                    className={`${styles.input} ${
                      errors.assoObject && touched.assoObject
                        ? styles.inputError
                        : ""
                    }`}
                    type="text"
                    name="assoObject"
                    placeholder="Objet de l'association"
                  />
                  {errors.assoObject && touched.assoObject ? (
                    <div className={styles.errorMessage}>
                      {errors.assoObject}
                    </div>
                  ) : null}
                </div>
                <div className={styles.formSection}>
                  <label className={styles.label}>
                    Adresse du sige social:
                  </label>
                  <Field
                    className={`${styles.input} ${
                      errors.assoAddress && touched.assoAddress
                        ? styles.inputError
                        : ""
                    }`}
                    type="text"
                    name="assoAddress"
                    placeholder="N de voie et Rue"
                  />
                  {errors.assoAddress && touched.assoAddress ? (
                    <div className={styles.errorMessage}>
                      {errors.assoAddress}
                    </div>
                  ) : null}
                </div>

                <div className="row-space-between align-flex-start">
                  <div
                    className={`${styles.formSection} two-third  ${styles.cityInput}`}
                  >
                    <label className={styles.label}>Ville :</label>
                    <Field
                      className={`${styles.input} ${
                        errors.assoCity && touched.assoCity
                          ? styles.inputError
                          : ""
                      }`}
                      type="text"
                      name="assoCity"
                      placeholder="Ville"
                    />
                    {errors.assoCity && touched.assoCity ? (
                      <div className={styles.errorMessage}>
                        {errors.assoCity}
                      </div>
                    ) : null}
                  </div>
                  <div
                    className={`${styles.formSection} one-third  ${styles.postCode}`}
                  >
                    <label className={styles.label}>Code postal :</label>
                    <Field
                      className={`${styles.input} ${
                        errors.assoPostCode && touched.assoPostCode
                          ? styles.inputError
                          : ""
                      }`}
                      type="text"
                      name="assoPostCode"
                      placeholder="Code postal"
                    />
                    {errors.assoPostCode && touched.assoPostCode ? (
                      <div className={styles.errorMessage}>
                        {errors.assoPostCode}
                      </div>
                    ) : null}
                  </div>
                </div>
                <div className={`${styles.buttons} row-space-between`}>
                  <div
                    onClick={() => setStep(1)}
                    className={`outlined-button ${styles.previousButton}`}
                  >
                    <Arrow className={styles.previousArrow} />
                    <span>Prcdent</span>
                  </div>
                  <button
                    type="submit"
                    onClick={() => {
                      if (Object.keys(errors).length !== 0) {
                        setIsErrors(true);
                      } else {
                        setIsErrors(false);
                      }
                    }}
                    className="primary-button"
                  >
                    <span>Suivant</span>
                    <Arrow className={styles.nextArrow} />
                  </button>
                </div>
              </div>
            </Form>
          )}
        </Formik>

        <Formik
          initialValues={{
            assoNumber: "",
            assoSiretNumber: "",
          }}
          validationSchema={fourthSchema}
          onSubmit={async (values) => {
            if (values.assoSiretNumber) {
              let data = values.assoSiretNumber;
              await api
                .post("association/create/check/siret", { data })
                .then((res) => {
                  if (res.data.exists) {
                    setNumberError(true);
                    setNotification({
                      type: "error",
                      state: "shown",
                      message: "N de SIRET dj utilis",
                    });
                    setStep(3);
                  } else {
                    setFormValues((prevFormValues) => ({
                      ...prevFormValues,
                      assoNumber: values.assoNumber,
                      assoSiretNumber: values.assoSiretNumber,
                    }));
                    setNumberError(false);
                    setStep(4);
                  }
                });
            } else if (values.assoNumber) {
              let data = values.assoNumber;
              await api
                .post("association/create/check/association-number", { data })
                .then((res) => {
                  if (res.data.exists) {
                    setNumberError(true);
                    setNotification({
                      type: "error",
                      state: "shown",
                      message: "N d'association dj utilis",
                    });
                    setStep(3);
                  } else {
                    setFormValues((prevFormValues) => ({
                      ...prevFormValues,
                      assoNumber: values.assoNumber,
                      assoSiretNumber: values.assoSiretNumber,
                    }));
                    setNumberError(false);
                    setStep(4);
                  }
                });
            }
          }}
        >
          {({ errors, touched }) => (
            <Form>
              <div
                className={`${styles.smallStep} ${
                  step !== 3 ? styles.hiddenStep : ""
                }`}
              >
                <h1 className={styles.title}>Un dernier effort !</h1>
                <p className={styles.description}>
                  Veuillez renseigner le n de SIRET de votre association ou le
                  numro national de votre association.
                </p>
                <div className={styles.formSection}>
                  <div className={styles.numberSelector}>
                    <label
                      htmlFor="siret"
                      className={`${styles.numberType} ${
                        numberType === "siret" ? styles.numberSelected : ""
                      }`}
                      onClick={() => setNumberType("siret")}
                    >
                      N de SIRET
                    </label>
                    <Field
                      className="display-none"
                      type="radio"
                      value="siret"
                      name="numberType"
                      id="siret"
                      selected
                    />

                    <label
                      htmlFor="assoNumber"
                      className={`${styles.numberType} ${
                        numberType === "association"
                          ? styles.numberSelected
                          : ""
                      }`}
                      onClick={() => setNumberType("association")}
                    >
                      N d'association
                    </label>

                    <Field
                      className="display-none"
                      type="radio"
                      value="associationNumber"
                      name="numberType"
                      id="assoNumber"
                    />
                  </div>

                  {numberType === "siret" && (
                    <div className={styles.formSection}>
                      <label htmlFor="" className={styles.label}>
                        Numro de SIRET :
                      </label>

                      <Field
                        className={`${styles.input} ${
                          errors.assoSiretNumber && touched.assoSiretNumber
                            ? styles.inputError
                            : ""
                        }`}
                        type="text"
                        placeholder="N de SIRET"
                        name="assoSiretNumber"
                      />

                      {numberType === "siret" &&
                      errors.assoSiretNumber &&
                      touched.assoSiretNumber ? (
                        <div className={styles.errorMessage}>
                          {errors.assoSiretNumber}
                        </div>
                      ) : null}
                    </div>
                  )}

                  {numberType === "association" && (
                    <div className={styles.formSection}>
                      <label htmlFor="" className={styles.label}>
                        Numro de l'association :
                      </label>
                      <Field
                        className={`${styles.input} ${
                          errors.assoNumber && touched.assoNumber
                            ? styles.inputError
                            : ""
                        }`}
                        type="text"
                        placeholder="N d'association"
                        name="assoNumber"
                      />

                      {numberType === "association" &&
                      errors.assoNumber &&
                      touched.assoNumber ? (
                        <div className={styles.errorMessage}>
                          {errors.assoNumber}
                        </div>
                      ) : null}
                    </div>
                  )}

                  <div className={`${styles.buttons} row-space-between`}>
                    <div
                      onClick={() => {
                        setStep(2);
                      }}
                      className={`outlined-button ${styles.previousButton}`}
                    >
                      <Arrow className={styles.previousArrow} />
                      <span>Prcdent</span>
                    </div>
                    <button
                      type="submit"
                      onClick={() => {
                        if (Object.keys(errors).length !== 0) {
                          setIsErrors(true);
                        } else {
                          setIsErrors(false);
                        }
                      }}
                      className="primary-button"
                    >
                      <span>Suivant</span>
                      <Arrow className={styles.nextArrow} />
                    </button>
                  </div>
                </div>
              </div>
            </Form>
          )}
        </Formik>

        <div
          className={`${styles.smallStep} ${
            step !== 4 ? styles.hiddenStep : ""
          }`}
        >
          <h1 className={styles.title}>Tout est ok !</h1>
          <p className={styles.description}>
            Votre association a bien t enregistre sur AnimaZone !<br />
            Vous pouvez  prsent accder au panel de gestion de votre
            association.
          </p>

          <div className={styles.formSection}>
            <div className={`${styles.buttons} row-space-between`}>
              <Link
                to="/associations/panel/dashboard"
                className="primary-button"
              >
                <span>C'est parti !</span>
                <Arrow className={styles.nextArrow} />
              </Link>
            </div>
          </div>
        </div>
      </div>
    );
  }
};

export default withRouter(CreateAssociation);
