import styles from "./AssoProfile.module.css";

import { useContext, useEffect, useState } from "react";
import { Link } from "react-router-dom";

import { ReactComponent as Download } from "./download.svg";
import { ReactComponent as Web } from "./Web.svg";
import { ReactComponent as Facebook } from "./Facebook.svg";
import { ReactComponent as Twitter } from "./Twitter.svg";
import { ReactComponent as Instagram } from "./Instagram.svg";

import NotificationContext from "./../../../../../contexts/notificationContext";

import { Formik, Form, Field } from "formik";
import * as Yup from "yup";
import api from "./../../../../../services/api";

const AssoProfile = () => {
  const { setNotification } = useContext(NotificationContext);
  const [filename, setFilename] = useState("Choisir un fichier");

  const [data, setData] = useState([]);
  const [ok, setOk] = useState(false);

  useEffect(() => {
    let cancelled = false;
    api.get(`/associations/public/`).then((res) => {
      if (cancelled) return;
      document.title = `${res.data.name} | Profil public`;
      setData(res.data);
    });

    return () => (cancelled = true);
  }, [ok]);

  let phoneRegEx =
    /^(?:(?:\+|00)33[\s.-]{0,3}(?:\(0\)[\s.-]{0,3})?|0)[1-9](?:(?:[\s.-]?\d{2}){4}|\d{2}(?:[\s.-]?\d{3}){2})$/i;
  let addressRegEx = /^[0-9]{1,3}(?:(?:[,. ]){1}[-a-zA-Z]+)+$/i;
  let postCodeRegEx = /^(([0-8][0-9])|(9[0-5])|(2[ab]))[0-9]{3}$/i;
  let sentenceRegEx = /^([^0-9]*)$/i;

  const assoProfileSchema = Yup.object().shape({
    name: Yup.string().required("Champ requis"),
    description: Yup.string().required("Champ requis"),
    email: Yup.string().email("Email invalide").required("Champ requis"),
    phone: Yup.string()
      .matches(phoneRegEx, "Numro non valide")
      .required("Champ requis"),
    address: Yup.string()
      .matches(addressRegEx, "Adresse non valide")
      .required("Champ requis"),
    city: Yup.string()
      .matches(sentenceRegEx, "Ville non valide")
      .required("Champ requis"),
    post_code: Yup.string()
      .matches(postCodeRegEx, "Code Postal non valide")
      .required("Champ requis"),
    website: Yup.string(),
    facebook: Yup.string(),
    instagram: Yup.string(),
    twitter: Yup.string(),
  });

  return (
    <div className={styles.content}>
      <h1 className={styles.pageTitle}>Votre profil public :</h1>
      <div>
        {data && (
          <div className={styles.profileHeader}>
            <span className={styles.headerText}>
              Votre profil est complet !
            </span>
            <Link
              to={`/association/${data.association_id}`}
              className="primary-button"
            >
              <span>Voir mon profil</span>
            </Link>
          </div>
        )}
        {!data && (
          <div className={styles.profileHeader}>
            <span className={styles.headerText}>
              Vous n'avez pas complt votre profil public, veuillez le
              complter
            </span>
          </div>
        )}
      </div>
      <div className={styles.profileBody}>
        <h2 className={styles.modifyTitle}>
          {data ? "Modifier votre profil" : "Complter votre profil :"}{" "}
        </h2>
        <Formik
          initialValues={{
            name: data ? data.name : "",
            description: data ? data.description : "",
            image: data ? data.image : {},
            email: data ? data.email : "",
            phone: data ? data.phone : "",
            address: data ? data.address : "",
            city: data ? data.city : "",
            post_code: data ? data.post_code : "",
            website: data && data.website !== "undefined" ? data.website : "",
            facebook:
              data && data.facebook !== "undefined" ? data.facebook : "",
            instagram:
              data && data.instagram !== "undefined" ? data.instagram : "",
            twitter: data && data.twitter !== "undefined" ? data.twitter : "",
          }}
          enableReinitialize="true"
          validationSchema={assoProfileSchema}
          onSubmit={(values) => {
            setOk(false);
            let formData = new FormData();
            for (let value in values) {
              formData.append(value, values[value]);
            }

            setNotification({
              type: "loading",
              state: "shown",
              message: "Chargement en cours...",
            });

            api
              .put(`/associations/edit/`, formData)
              .then(() => {
                setNotification({
                  type: "success",
                  state: "shown",
                  message: "Profil mis  jour",
                });
                setOk(true);
              })
              .catch((err) => {
                setNotification({
                  type: "error",
                  state: "shown",
                  message: err.response.data.message,
                });
              });
          }}
        >
          {({ errors, touched, setFieldValue }) => (
            <Form>
              <h3 className={styles.sectionTitle}>Vos informations :</h3>
              <div className={styles.formSection}>
                <label className={styles.label}>Nom d'affichage :</label>
                <Field
                  className={`secondary-input ${
                    errors.name && touched.name ? "secondary-input-error" : ""
                  }`}
                  name="name"
                  type="text"
                  placeholder="Nom"
                  defaultValue={data ? data.name : ""}
                />
                {errors.name && touched.name ? (
                  <div className="error-message">{errors.name}</div>
                ) : null}
              </div>
              <div className={styles.formSection}>
                <label className={styles.label}>Descritpion :</label>
                <Field
                  className={`secondary-input ${styles.textarea}  ${
                    errors.description && touched.description
                      ? "secondary-input-error"
                      : ""
                  }`}
                  name="description"
                  as="textarea"
                  defaultValue={data ? data.description : ""}
                  placeholder="Description"
                />
                {errors.description && touched.description ? (
                  <div className="error-message">{errors.description}</div>
                ) : null}
              </div>
              <div className={styles.formSection}>
                <label className={styles.label}>Image :</label>
                <p className={styles.infoText}>
                  Pour un meilluer rendu, privilgiez les images de taille
                  950x350 ou de mme ratio
                </p>
                <label htmlFor="file" className={styles.fileLabel}>
                  <Download />
                  <span>{filename}</span>
                </label>
                <input
                  className={styles.fileInput}
                  type="file"
                  name="image"
                  id="file"
                  accept="image/png, image/gif, image/jpeg, image/webp"
                  onChange={(e) => {
                    let file = e.target.files[0];
                    setFieldValue("image", file);
                    setFilename(e.target.value.split("\\").pop());
                  }}
                />
                {errors.image && touched.image ? (
                  <div className="error-message">{errors.image}</div>
                ) : null}
              </div>
              <h3 className={styles.sectionTitle}>Coordonnes affiches :</h3>
              <div className={styles.formSection}>
                <label className={styles.label}>Email :</label>
                <Field
                  className={`secondary-input ${
                    errors.email && touched.email ? "secondary-input-error" : ""
                  }`}
                  name="email"
                  type="mail"
                  defaultValue={data ? data.email : ""}
                  placeholder="Email"
                />
                {errors.email && touched.email ? (
                  <div className="error-message">{errors.email}</div>
                ) : null}
              </div>
              <div className={styles.formSection}>
                <label className={styles.label}>Tlphone :</label>
                <Field
                  className={`secondary-input ${
                    errors.phone && touched.phone ? "secondary-input-error" : ""
                  }`}
                  name="phone"
                  type="text"
                  defaultValue={data ? data.phone : ""}
                  placeholder="N de tlphone"
                />
                {errors.phone && touched.phone ? (
                  <div className="error-message">{errors.phone}</div>
                ) : null}
              </div>
              <div className={styles.formSection}>
                <label className={styles.label}>Addresse :</label>
                <Field
                  className={`secondary-input ${
                    errors.address && touched.address
                      ? "secondary-input-error"
                      : ""
                  }`}
                  name="address"
                  type="text"
                  defaultValue={data ? data.address : ""}
                  placeholder="Adresse"
                />
                {errors.address && touched.address ? (
                  <div className="error-message">{errors.address}</div>
                ) : null}
              </div>
              <div className={styles.formSection}>
                <div className={`row align-flex-start ${styles.row}`}>
                  <div className={styles.halfSection}>
                    <label className={styles.label}>Ville :</label>
                    <Field
                      className={`secondary-input ${
                        errors.city && touched.city
                          ? "secondary-input-error"
                          : ""
                      }`}
                      name="city"
                      type="text"
                      defaultValue={data ? data.city : ""}
                      placeholder="Ville"
                    />
                    {errors.city && touched.city ? (
                      <div className="error-message">{errors.city}</div>
                    ) : null}
                  </div>
                  <div className={styles.halfSection}>
                    <label className={styles.label}>Code Postal :</label>
                    <Field
                      className={`secondary-input ${
                        errors.post_code && touched.post_code
                          ? "secondary-input-error"
                          : ""
                      }`}
                      name="post_code"
                      type="text"
                      defaultValue={data ? data.post_code : ""}
                      placeholder="Code Postal"
                    />
                    {errors.post_code && touched.post_code ? (
                      <div className="error-message">{errors.post_code}</div>
                    ) : null}
                  </div>
                </div>
              </div>
              <h3 className={styles.sectionTitle}>Vos rseaux sociaux :</h3>
              <p>
                Vous pouvez ajouter des liens vers vos profils sur les rseaux
                sociaux
              </p>
              <div className={styles.formSection}>
                <div className={styles.social}>
                  <Web />
                  <Field
                    className="secondary-input"
                    name="website"
                    type="text"
                    defaultValue={
                      data && data.website !== "undefined" ? data.website : ""
                    }
                    placeholder="http:// Site Web"
                    pattern="https://.*"
                  />
                </div>
                <div className={styles.social}>
                  <Facebook />
                  <Field
                    className="secondary-input"
                    name="facebook"
                    type="url"
                    defaultValue={
                      data.facebook !== "undefined" ? data.facebook : ""
                    }
                    placeholder="http:// Facebook"
                  />
                </div>
                <div className={styles.social}>
                  <Instagram />
                  <Field
                    className="secondary-input"
                    name="instagram"
                    type="url"
                    defaultValue={
                      data && data.instagram !== "undefined"
                        ? data.instagram
                        : ""
                    }
                    placeholder="http:// Instagram"
                  />
                </div>
                <div className={styles.social}>
                  <Twitter />
                  <Field
                    className="secondary-input"
                    name="twitter"
                    type="url"
                    defaultValue={
                      data && data.twitter !== "undefined" ? data.twitter : ""
                    }
                    placeholder="http:// Twitter"
                  />
                </div>
              </div>
              <button type="submit" className="primary-button">
                {data ? "Mettre  jour le profil" : "Complter mon profil"}
              </button>
            </Form>
          )}
        </Formik>
      </div>
    </div>
  );
};

export default AssoProfile;
