import styles from "./Auth.module.css";
import { Formik, Form, Field } from "formik";
import axios from "axios";
import * as Yup from "yup";
import { useEffect, useContext } from "react";
import { currentPageContext } from "./../../../../contexts/shared/currentPageContext";

const ResetPassword = () => {
  const { setCurrentPage } = useContext(currentPageContext);

  useEffect(() => {
    setCurrentPage("");
  }, []);

  const ResetPasswordSchema = Yup.object().shape({
    password: Yup.string().required("Champ Requis"),
    confirm_password: Yup.string()
      .oneOf(
        [Yup.ref("password"), null],
        "Les mots de passes ne correspondent pas"
      )
      .required("Champ requis"),
  });

  return (
    <div className={styles.resetPassword}>
      <h1 className={styles.title}>
        Rinitialiser votre
        <br /> mot de passe :
      </h1>
      <p>Saisissez un nouveau mot de passe pour "INSERER EMAIL"</p>

      <Formik
        initialValues={{
          password: "",
        }}
        validationSchema={ResetPasswordSchema}
        onSubmit={async (values) => {
          await axios.post("http://localhost:8080/reset-password/", values);
        }}
      >
        {({ errors, touched }) => (
          <Form>
            <div className={styles.formSection}>
              <label>Nouveau mot de passe :</label>
              <Field
                className={`primary-input ${
                  (errors.password && touched.password) ||
                  (errors.confirm_password && touched.confirm_password)
                    ? "primary-input-error"
                    : ""
                }`}
                type="password"
                name="password"
                placeholder="Mot de passe"
                required
              />
              {errors.password && touched.password ? (
                <div className="error-message">{errors.password}</div>
              ) : null}
            </div>
            <div className={styles.formSection + " " + styles.forgotSection}>
              <label>Confirmer nouveau mot de passe :</label>
              <Field
                className={`primary-input ${
                  errors.confirm_password && touched.confirm_password
                    ? "primary-input-error"
                    : ""
                }`}
                type="password"
                name="confirm_password"
                placeholder="Confirmer mot de passe"
                required
              />
              {errors.confirm_password && touched.confirm_password ? (
                <div className="error-message">{errors.confirm_password}</div>
              ) : null}
            </div>
            <button type="submit" className="primary-button">
              Rinitialiser le mot de passe
            </button>
          </Form>
        )}
      </Formik>
    </div>
  );
};

export default ResetPassword;
