import SearchBar from "../../shared/SearchBar/SearchBar.jsx";
import styles from "./AdoptMe.module.css";
import React, { useState, useEffect, useContext } from "react";

import { queryContext } from "../../../contexts/shared/queryContext.js";
import { currentPageContext } from "../../../contexts/shared/currentPageContext.js";
import api from "./../../../services/api";
import Pagination from "./../../shared/Pagination/Pagination";
import Results from "./Results.jsx";

const AdoptMe = () => {
  const { setCurrentPage } = useContext(currentPageContext);
  const [listings, setListings] = useState([]);
  const [category, setCategory] = useState("all");
  const [query, setQuery] = useState("");
  const [lastQuery, setLastQuery] = useState("");
  const [search, setSearch] = useState("");

  const [page, setPage] = useState(1);

  useEffect(() => {
    if (search) {
      let queryCategory = "all";
      if (category === "Chats") queryCategory = "cats";
      if (category === "Chiens") queryCategory = "dogs";
      if (category === "Lapins") queryCategory = "rabbits";
      if (category === "Rongeurs") queryCategory = "roddents";
      if (category === "Volatiles") queryCategory = "birds";

      api
        .get(`/search/query=${query}&category=${queryCategory}}`)
        .then((req) => {
          setListings(req.data);
          setLastQuery(query);
          setSearch(false);
        });
    }
  }, [search]);
  const resultsPerPage = 18;
  const indexOfLastResult = page * resultsPerPage;
  const indexOfFirstResult = indexOfLastResult - resultsPerPage;
  const currentResults = listings.slice(indexOfFirstResult, indexOfLastResult);

  useEffect(() => {
    setCurrentPage("adopt-me");
    api.get("/listings/").then((req) => {
      console.log(req.data);
      setListings(req.data);
    });
  }, []);

  useEffect(() => {
    switch (category) {
      case "all":
        api.get("/listings/").then((req) => {
          setListings(req.data);
        });
        break;
      case "Chats":
        api.get("/listings/cat").then((req) => {
          setListings(req.data);
        });
        break;
      case "Chiens":
        api.get("/listings/dog").then((req) => {
          setListings(req.data);
        });
        break;
      case "Lapins":
        api.get("/listings/rabbit").then((req) => {
          setListings(req.data);
        });
        break;
      case "Volatiles":
        api.get("/listings/bird").then((req) => {
          setListings(req.data);
        });
        break;
      case "Rongeurs":
        api.get("/listings/roddent").then((req) => {
          setListings(req.data);
        });
        break;
      default:
        api.get("/listings/").then((req) => {
          setListings(req.data);
        });
    }
  }, [category]);

  return (
    <>
      <div className={styles.adoptMe}>
        <queryContext.Provider
          value={{ category, setCategory, query, setQuery, search, setSearch }}
        >
          <SearchBar className={styles.searchBar} />
        </queryContext.Provider>
        <Results listings={currentResults} query={lastQuery} />
        <Pagination
          resultsPerPage={resultsPerPage}
          resultsNumber={listings.length}
          setPage={setPage}
          page={page}
        />
      </div>
    </>
  );
};

export default AdoptMe;
