import styles from "./SearchBarSelect.module.css";
import { ReactComponent as Chevron } from "./chevron-down.svg";

import { useContext, useState } from "react";

import { queryContext } from "../../../../contexts/shared/queryContext";

const SearchBarSelect = () => {
  const [isSwitchedOn, setIsSwitchedOn] = useState(false);
  const { category, setCategory } = useContext(queryContext);
  return (
    <div>
      <div className={styles.selectBox}>
        <div
          className={styles.selected}
          onClick={() => setIsSwitchedOn(!isSwitchedOn)}
        >
          <span>{category === "all" ? "Animal" : category}</span>
          <Chevron />
        </div>
        <div
          className={
            isSwitchedOn
              ? `${styles.optionsContainer} ${styles.active}`
              : styles.optionsContainer
          }
        >
          <div
            className={`${styles.option} ${styles.defaultOption}`}
            onClick={() => {
              setIsSwitchedOn(false);
              setCategory("all");
            }}
          >
            <label>Tous</label>
          </div>

          <div
            className={styles.option}
            onClick={() => {
              setIsSwitchedOn(false);
              setCategory("Chiens");
            }}
          >
            <input
              type="radio"
              className={styles.radio}
              name="category"
              id="dogs"
            />
            <label htmlFor="dogs">Chiens</label>
          </div>

          <div
            className={styles.option}
            onClick={() => {
              setIsSwitchedOn(false);
              setCategory("Chats");
            }}
          >
            <input
              type="radio"
              className={styles.radio}
              name="category"
              id="cats"
            />
            <label htmlFor="cats">Chats</label>
          </div>

          <div
            className={styles.option}
            onClick={() => {
              setIsSwitchedOn(false);
              setCategory("Lapins");
            }}
          >
            <input
              type="radio"
              className={styles.radio}
              name="category"
              id="rabbits"
            />
            <label htmlFor="rabbits">Lapins</label>
          </div>

          <div
            className={styles.option}
            onClick={() => {
              setIsSwitchedOn(false);
              setCategory("Volatiles");
            }}
          >
            <input
              type="radio"
              className={styles.radio}
              name="category"
              id="birds"
            />
            <label htmlFor="birds">Volatiles</label>
          </div>

          <div
            className={styles.option}
            onClick={() => {
              setIsSwitchedOn(false);
              setCategory("Rongeurs");
            }}
          >
            <input
              type="radio"
              className={styles.radio}
              name="category"
              id="rodents"
            />
            <label htmlFor="rodents">Rongeurs</label>
          </div>
        </div>
      </div>
    </div>
  );
};

export default SearchBarSelect;

