import styles from "./Auth.module.css";
import { Link, Redirect } from "react-router-dom";
import { useState, useContext, useEffect } from "react";

import { Formik, Form, Field } from "formik";
import Cookies from "js-cookie";
import * as Yup from "yup";

import notificationContext from "../../../../contexts/notificationContext";
import authContext from "../../../../contexts/authContext";
import { currentPageContext } from "./../../../../contexts/shared/currentPageContext";
import api from "./../../../../services/api";

const Register = () => {
  const [isSuccess, setIsSuccess] = useState(false);
  const { setNotification } = useContext(notificationContext);
  const { isAuthenticated, setIsAuthenticated } = useContext(authContext);
  const { setCurrentPage } = useContext(currentPageContext);

  useEffect(() => {
    setCurrentPage("");
  }, []);

  if (isSuccess) {
    return <Redirect to="/login" />;
  }

  if (
    Cookies.get("authenticated") === true &&
    Cookies.get("x-access-token") &&
    isAuthenticated === false
  ) {
    setIsAuthenticated(true);
  }

  if (isAuthenticated) {
    return <Redirect to="/" />;
  }

  let passwordRegEx = /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)[a-zA-Z\d]{8,}$/i;
  let sentenceRegEx = /^([^0-9]*)$/i;

  const RegisterSchema = Yup.object().shape({
    firstname: Yup.string()
      .matches(sentenceRegEx, "Prnom non valide")
      .required("Champ requis"),
    lastname: Yup.string()
      .matches(sentenceRegEx, "Nom non valide")
      .required("Champ requis"),
    email: Yup.string().email("Email invalide").required("Champ Requis"),
    password: Yup.string()
      .min(8, "Le mot de passe doit faire au moins 8 caractres")
      .matches(
        passwordRegEx,
        "Le mot de passe doit contenir au moins une majuscule, une minuscule et un chiffre"
      )
      .required("Champ Requis"),
    confirm_password: Yup.string()
      .oneOf(
        [Yup.ref("password"), null],
        "Les mots de passes ne correspondent pas"
      )
      .required("Champ requis"),
  });

  return (
    <div className={styles.register}>
      <h1 className={styles.title}>Crer un compte</h1>
      <Formik
        initialValues={{
          lastname: "",
          firstname: "",
          email: "",
          password: "",
        }}
        validationSchema={RegisterSchema}
        onSubmit={async (values) => {
          await api
            .post("/register", values)
            .then((res) => {
              if (res.data.success) {
                setIsSuccess(true);
                setNotification({
                  state: "shown",
                  type: "success",
                  message: "Votre compte a bien t cr",
                });
              }
            })
            .catch((error) => {
              setNotification({
                state: "shown",
                type: "error",
                message: error.response.data.error,
              });
            });
        }}
      >
        {({ errors, touched }) => (
          <Form>
            <div className="row-space-between align-flex-start">
              <div className={`${styles.formSection} ${styles.half}`}>
                <label>Nom :</label>
                <Field
                  type="text"
                  className={`primary-input ${
                    errors.lastname && touched.lastname
                      ? "primary-input-error"
                      : ""
                  }`}
                  name="lastname"
                  placeholder="Nom"
                />
                {errors.lastname && touched.lastname ? (
                  <div className="error-message">{errors.lastname}</div>
                ) : null}
              </div>
              <div className={`${styles.formSection} ${styles.half}`}>
                <label>Prnom :</label>
                <Field
                  type="text"
                  className={`primary-input ${
                    errors.firstname && touched.firstname
                      ? "primary-input-error"
                      : ""
                  }`}
                  name="firstname"
                  placeholder="Prnom"
                />
                {errors.firstname && touched.firstname ? (
                  <div className="error-message">{errors.firstname}</div>
                ) : null}
              </div>
            </div>
            <div className={styles.formSection}>
              <label>Email :</label>
              <Field
                type="mail"
                className={`primary-input ${
                  errors.email && touched.email ? "primary-input-error" : ""
                }`}
                name="email"
                placeholder="Email"
              />
              {errors.email && touched.email ? (
                <div className="error-message">{errors.email}</div>
              ) : null}
            </div>
            <div className={styles.formSection}>
              <label>Mot de passe :</label>
              <Field
                type="password"
                className={`primary-input ${
                  (errors.password && touched.password) ||
                  (errors.confirm_password &&
                    touched.confirm_password &&
                    errors.password &&
                    touched.password)
                    ? "primary-input-error"
                    : ""
                }`}
                name="password"
                placeholder="Mot de passe"
              />
              {errors.password && touched.password ? (
                <div className="error-message">{errors.password}</div>
              ) : null}
            </div>
            <div className={styles.formSection}>
              <label>Confirmer mot de passe :</label>
              <Field
                className={`primary-input ${
                  errors.confirm_password &&
                  touched.confirm_password &&
                  touched.password
                    ? "primary-input-error"
                    : ""
                }`}
                type="password"
                name="confirm_password"
                placeholder="Confirmer mot de passe"
              />
              {errors.confirm_password &&
              touched.confirm_password &&
              touched.password ? (
                <div className="error-message">{errors.confirm_password}</div>
              ) : null}
            </div>
            <div className={styles.row + " " + styles.bottomRow}>
              <button type="submit" className="primary-button">
                Crer un compte
              </button>

              <div className={styles.connectText}>
                <div>Dja un compte ?</div>
                <Link to="/login">Se connecter</Link>
              </div>
            </div>
          </Form>
        )}
      </Formik>
    </div>
  );
};

export default Register;
