import styles from "./Help.module.css";

import { ReactComponent as Mail } from "./mail.svg";
import { ReactComponent as Phone } from "./phone.svg";

import { useEffect } from "react";

import api from "../../../../../services/api";

const Help = () => {
  useEffect(() => {
    api.get("/associations/public/").then((res) => {
      document.title = `${res.data.name} | Aide`;
    });
  }, []);

  return (
    <div className={styles.content}>
      <h1 className={styles.pageTitle}>Un problme ?</h1>
      <div className={styles.helpHeader}>
        <h2 className={styles.title}>Nous contacter :</h2>
        <ul className={styles.contactList}>
          <li className={styles.contact}>
            <Mail />
            <span>loulou.lecouturier@gmail.com</span>
          </li>
          <li className={styles.contact}>
            <Phone />
            <span>0652025415</span>
          </li>
        </ul>
      </div>
    </div>
  );
};

export default Help;
