import styles from "./Pagination.module.css";
import { ReactComponent as Arrow } from "./arrow-right.svg";

const Pagination = ({ resultsPerPage, resultsNumber, setPage, page }) => {
  let pageNumbers = [];

  for (let i = 1; i < Math.ceil(resultsNumber / resultsPerPage); i++) {
    pageNumbers.push(i);
  }

  if (pageNumbers.length >= 8) {
    let pages = [];
    if (page > 3) {
      let firstPages = pageNumbers.slice(page, page + 4);
      pages = [page - 2, page - 1, page, ...firstPages, "..."];

      if (page >= pageNumbers.length - 4) {
        let firstPages = pageNumbers.slice(pageNumbers.length - 8, page - 1);
        let lastPages = pageNumbers.slice(page, pageNumbers.length);
        pages = [...firstPages, page, ...lastPages];
      }
    } else {
      let firstPages = pageNumbers.slice(0, 7);
      pages = [...firstPages, "..."];
    }

    return (
      <nav className={styles.paginationContainer}>
        <Arrow
          className={`${styles.arrowLeft} ${page === 1 && styles.hidden}`}
          onClick={() => page !== 1 && setPage(page - 1)}
        />
        <ul>
          {pages.map((number) => {
            if (number === "...") {
              return (
                <li
                  key={number}
                  className={`${styles.pageNumber} ${styles.dots}`}
                >
                  {number}
                </li>
              );
            } else {
              return (
                <li
                  onClick={() => setPage(number)}
                  key={number}
                  className={`${styles.pageNumber} ${
                    page === number ? styles.currentPage : ""
                  }`}
                >
                  {number}
                </li>
              );
            }
          })}
        </ul>
        <Arrow
          className={`${styles.arrowRight} ${
            page === pageNumbers.length && styles.hidden
          }`}
          onClick={() => page !== pageNumbers.length && setPage(page + 1)}
        />
      </nav>
    );
  } else if (pageNumbers.length > 1) {
    return (
      <nav className={styles.paginationContainer}>
        <Arrow
          className={`${styles.arrowLeft} ${page === 1 && styles.hidden}`}
          onClick={() => page !== 1 && setPage(page - 1)}
        />
        <ul>
          {pageNumbers.map((number) => {
            return (
              <li
                onClick={() => setPage(number)}
                key={number}
                className={`${styles.pageNumber} ${
                  page === number ? styles.currentPage : ""
                }`}
              >
                {number}
              </li>
            );
          })}
        </ul>
        <Arrow
          className={`${styles.arrowRight} ${
            page === pageNumbers.length ? styles.hidden : ""
          }`}
          onClick={() => page !== pageNumbers.length && setPage(page + 1)}
        />
      </nav>
    );
  } else {
    return <></>;
  }
};

export default Pagination;
