import styles from "./Auth.module.css";
import { Formik, Form, Field } from "formik";
import axios from "axios";
import { useContext, useEffect } from "react";
import { currentPageContext } from "./../../../../contexts/shared/currentPageContext";
import * as Yup from "yup";

const ForgotPassword = () => {
  const { setCurrentPage } = useContext(currentPageContext);
  const forgotPasswordSchema = Yup.object().shape({
    email: Yup.string().email("Email invalide").required("Champ Requis"),
  });

  useEffect(() => {
    setCurrentPage("");
  }, []);

  return (
    <div className={styles.forgotPassword}>
      <h1>Mot de passe oubli ?</h1>
      <p>
        Vous recevrez un lien pour rinitialiser votre mot de passe par mail
      </p>
      <Formik
        initialValues={{
          email: "",
        }}
        validationSchema={forgotPasswordSchema}
        onSubmit={async (values) => {
          await axios.post("http://localhost:8080/forgot-password", values);
        }}
      >
        {({ errors, touched }) => (
          <Form>
            <div className={styles.formSection + " " + styles.forgotSection}>
              <label>Votre email :</label>
              <Field
                className={`primary-input ${
                  errors.email && touched.email ? "primary-input-error" : ""
                }`}
                type="email"
                name="email"
                placeholder="Email"
                required
              />
              {errors.email && touched.email ? (
                <div className="error-message">{errors.email}</div>
              ) : null}
            </div>
            <button type="submit" className="primary-button">
              Envoyez-moi un mail
            </button>
          </Form>
        )}
      </Formik>
    </div>
  );
};

export default ForgotPassword;
