import styles from "./Auth.module.css";
import { Link, Redirect, useLocation } from "react-router-dom";
import { useState, useContext, useEffect } from "react";

import { Formik, Form, Field } from "formik";
import * as Yup from "yup";
import Cookies from "js-cookie";

import userContext from "../../../../contexts/userContext";
import authContext from "../../../../contexts/authContext";
import { AssociationContext } from "../../../../contexts/associations/assoContext";
import notificationContext from "../../../../contexts/notificationContext";
import { currentPageContext } from "./../../../../contexts/shared/currentPageContext";
import api from "./../../../../services/api";

const Login = () => {
  const { isAuthenticated, setIsAuthenticated } = useContext(authContext);
  const { setUser } = useContext(userContext);
  const { setCurrentPage } = useContext(currentPageContext);
  const { setAssociation } = useContext(AssociationContext);
  const { setNotification } = useContext(notificationContext);

  const [connected, setConnected] = useState(false);

  const { state } = useLocation();

  useEffect(() => {
    setCurrentPage("");
  }, []);

  const LoginSchema = Yup.object().shape({
    email: Yup.string().email("Email invalide").required("Champ Requis"),
    password: Yup.string().required("Champ Requis"),
  });

  if (
    Cookies.get("authenticated") === true &&
    Cookies.get("x-access-token") &&
    isAuthenticated === false
  ) {
    setIsAuthenticated(true);
  }

  if (connected || isAuthenticated) {
    return <Redirect to={state?.from || "/"} />;
  }

  return (
    <div className={styles.login}>
      <h1 className={styles.title}>Connexion</h1>
      <Formik
        initialValues={{
          email: "",
          password: "",
          remember_me: false,
        }}
        validationSchema={LoginSchema}
        onSubmit={async (values) => {
          setNotification({
            state: "shown",
            type: "loading",
            message: "Connexion en cours...",
          });

          await api
            .post("/login", values)
            .then(async (res) => {
              const {
                accessToken,
                refreshToken,
                hasAssociation,
                association_uuid,
                association_id,
              } = res.data;

              Cookies.set("x-access-token", accessToken, {
                //secure: true
                expires: values.remember_me ? 15 : null,
              });
              Cookies.set("x-refresh-token", refreshToken, {
                //secure: true
                expires: values.remember_me ? 15 : null,
              });
              Cookies.set("hasAssociation", hasAssociation, {
                //secure: true
                expires: values.remember_me ? 15 : null,
              });
              Cookies.set("association_uuid", association_uuid, {
                //secure: true
                expires: values.remember_me ? 15 : null,
              });
              Cookies.set("association_id", association_id, {
                //secure: true
                expires: values.remember_me ? 15 : null,
              });
            })

            .then(async (res) => {
              api.get(`/associations/public/`).then((res) => {
                setAssociation(res.data);
              });
            })
            .catch((error) => {
              setNotification({
                state: "shown",
                type: "error",
                message: error.response.data.error,
              });
            });
          api.get("/user").then((res) => {
            Cookies.set("authenticated", true, {
              //secure: true
              expires: values.remember_me ? 15 : null,
            });
            setUser(res.data);
            setNotification({
              state: "shown",
              type: "success",
              message: "Connect !",
            });
            setConnected(true);
            setIsAuthenticated(true);
          });
        }}
      >
        {({ errors, touched }) => (
          <Form>
            <div className={styles.formSection}>
              <label>Email :</label>
              <Field
                type="mail"
                className={`primary-input ${
                  errors.email && touched.email ? "primary-input-error" : ""
                }`}
                name="email"
                placeholder="Email"
              />
              {errors.email && touched.email ? (
                <div className="error-message">{errors.email}</div>
              ) : null}
            </div>
            <div className={styles.formSection}>
              <label>Mot de passe :</label>
              <Field
                type="password"
                className={`primary-input ${
                  errors.password && touched.password
                    ? "primary-input-error"
                    : ""
                }`}
                name="password"
                placeholder="Mot de passe"
              />
              {errors.password && touched.password ? (
                <div className="error-message">{errors.password}</div>
              ) : null}
              <Link to="/forgot-password">Mot de passe oubli</Link>
            </div>
            <div className={`${styles.formSection} ${styles.rememberMe}`}>
              <Field type="checkbox" name="remember_me" />
              <label htmlFor="remember_me" className="no-select">
                Se souvenir de moi
              </label>
            </div>
            <div className={styles.row + " " + styles.bottomRow}>
              <button type="submit" className="primary-button">
                Se connecter
              </button>

              <div className={styles.connectText}>
                <div>Pas encore de compte ?</div>
                <Link to="/register">Crer un compte</Link>
              </div>
            </div>
          </Form>
        )}
      </Formik>
    </div>
  );
};

export default Login;
