import styles from "./Settings.module.css";

import { useState, useEffect, useContext } from "react";
import { Formik, Form, Field } from "formik";
import * as Yup from "yup";

import api from "./../../../../../services/api";
import Cookies from "js-cookie";
import Moment from "react-moment";
import "moment/locale/fr";
import notificationContext from "../../../../../contexts/notificationContext";
import { deletePopUpContext } from "./../../../../../contexts/shared/deletePopUpContext";
import authContext from "../../../../../contexts/authContext";
import userContext from "../../../../../contexts/userContext";

const Settings = () => {
  const { setNotification } = useContext(notificationContext);
  const { setIsAuthenticated } = useContext(authContext);
  const { setUser } = useContext(userContext);
  const { setDeletePopUp } = useContext(deletePopUpContext);
  const [data, setData] = useState([]);
  const [owner, setOwner] = useState([]);
  const [association, setAssociation] = useState([]);

  useEffect(() => {
    let association_id = Cookies.get("association_id");
    api.get(`/associations/public/${association_id}`).then((res) => {
      setData(res.data);
      document.title = `${res.data.name} | Paramtres`;
    });
    api.get("/association").then((res) => setAssociation(res.data));
    api.get("/associations/owner").then((res) => {
      setOwner(res.data);
    });
  }, []);

  const handleLogout = () => {
    api
      .post("/logout")
      .then((res) => {
        if (res) {
          Cookies.remove("x-access-token");
          Cookies.remove("x-refresh-token");
          Cookies.remove("authenticated");
          Cookies.remove("hasAssociation");
          Cookies.remove("association_uuid");
          Cookies.remove("association_id");
          setIsAuthenticated(false);
          setUser({});
          setNotification({
            state: "shown",
            type: "success",
            message: "Vous avez t dconnect",
          });
        }
      })
      .catch((error) => {
        setNotification({
          state: "shown",
          type: "error",
          message: error.response.data.error,
        });
      });
  };

  const handleDelete = () => {
    setDeletePopUp({
      isShown: true,
      toDelete: "asso",
      title: "Supprimer l'association ?",
      text: 'Pour confirmer la suppression de votre association, veuillez crire "Supprimer dfinitivement".\nSi vous avez publi des annonces, celles-ci seront galement supprimes.',
      textToEnter: "Supprimer dfinitivement",
      link: "/association/delete",
      buttonText: "Supprimer mon compte",
    });
  };

  let addressRegEx = /^[0-9]{1,3}(?:(?:[,. ]){1}[-a-zA-Z]+)+$/i;
  let phoneRegEx =
    /^(?:(?:\+|00)33[\s.-]{0,3}(?:\(0\)[\s.-]{0,3})?|0)[1-9](?:(?:[\s.-]?\d{2}){4}|\d{2}(?:[\s.-]?\d{3}){2})$/i;

  let postCodeRegEx = /^(([0-8][0-9])|(9[0-5])|(2[ab]))[0-9]{3}$/i;
  let sentenceRegEx = /^([^0-9]*)$/i;

  const ownerSchema = Yup.object().shape({
    gender: Yup.string().required("Champ Requis"),
    lastname: Yup.string("Nom invalide")
      .matches(sentenceRegEx, "Nom invalide")
      .required("Champ requis"),
    firstname: Yup.string("Prnom invalide")
      .matches(sentenceRegEx, "Prnom invalide")
      .required("Champ requis"),
    email: Yup.string().email("Email invalide").required("Champ Requis"),
    phone: Yup.string()
      .matches(phoneRegEx, "Numro non valide")
      .required("Champ requis"),
    address: Yup.string()
      .matches(addressRegEx, "Adresse non valide")
      .required("Champ requis"),
    city: Yup.string()
      .matches(sentenceRegEx, "Ville non valide")
      .required("Champ requis"),
    post_code: Yup.string()
      .matches(postCodeRegEx, "Code Postal non valide")
      .required("Champ requis"),
  });

  const associationSchema = Yup.object().shape(
    {
      name: Yup.string("Nom invalide").required("Champ requis"),
      object: Yup.string("Nom invalide").required("Champ requis"),

      address: Yup.string()
        .matches(addressRegEx, "Adresse non valide")
        .required("Champ requis"),
      city: Yup.string()
        .matches(sentenceRegEx, "Ville non valide")
        .required("Champ requis"),
      post_code: Yup.string()
        .matches(postCodeRegEx, "Code Postal non valide")
        .required("Champ requis"),
      siret: Yup.string().when("assoNumber", {
        is: "",
        then: Yup.string()
          .min(14, "Numro non valide")
          .max(14, "Numro non valide")
          .required("Champ Requis"),
        otherwise: Yup.string()
          .min(14, "Numro non valide")
          .max(14, "Numro non valide"),
      }),
      assoNumber: Yup.string().when("siret", {
        is: "",
        then: Yup.string()
          .min(5, "Numro non valide")
          .max(100, "Numro non valide")
          .required("Champ requis"),
        otherwise: Yup.string()
          .min(5, "Numro non valide")
          .max(100, "Numro non valide"),
      }),
    },
    ["siret", "assoNumber"]
  );

  return (
    <div className={styles.settingsContent}>
      <h1 className={styles.pageTitle}>Paramtres :</h1>
      <div className={styles.content}>
        {data && (
          <div className={styles.settingsHeader}>
            <h2>{data.name}</h2>
            <span>
              Membre depuis le{" "}
              <Moment locale="fr" format="Do MMMM YYYY">
                {data.createdAt}
              </Moment>
            </span>
          </div>
        )}
        {owner && association ? (
          <div className={`row align-flex-start ${styles.row}`}>
            <div className={styles.infoContainer}>
              <Formik
                initialValues={{
                  name: association.name,
                  object: association.object,
                  address: association.address,
                  city: association.city,
                  post_code: association.post_code,
                  siret: association.siret,
                  assoNumber: association.assoNumber,
                }}
                validationSchema={associationSchema}
                enableReinitialize={true}
                onSubmit={(values) => {
                  setNotification({
                    state: "shown",
                    type: "loading",
                    message: "Modification en cours...",
                  });
                  api
                    .put("/associations/update/association", values)
                    .then(() =>
                      setNotification({
                        state: "shown",
                        type: "success",
                        message: "Changements appliqus !",
                      })
                    )
                    .catch((err) => {
                      setNotification({
                        state: "shown",
                        type: "error",
                        message: err.response.data.error,
                      });
                    });
                }}
              >
                {({ errors, touched }) => (
                  <Form>
                    <h2>Votre association :</h2>
                    <p className={styles.infoText}>
                      Il s'agit ici des informations lgales de votre
                      association, pas de celles qui sont affiches pour les
                      utilisateurs.
                    </p>

                    <div className={styles.formSection}>
                      <label className="label">Nom :</label>
                      <Field
                        className={`secondary-input ${
                          errors.name && touched.name
                            ? "secondary-input-error"
                            : ""
                        }`}
                        name="name"
                        type="text"
                        placeholder="Nom"
                      />
                      {errors.name && touched.name ? (
                        <div className="error-message">{errors.name}</div>
                      ) : null}
                    </div>
                    <div className={styles.formSection}>
                      <label className="label">Objet :</label>
                      <Field
                        className={`secondary-input ${
                          errors.object && touched.object
                            ? "secondary-input-error"
                            : ""
                        }`}
                        name="object"
                        type="text"
                        placeholder="Prnom"
                        defaultValue={association ? association.object : ""}
                      />
                      {errors.object && touched.object ? (
                        <div className="error-message">{errors.object}</div>
                      ) : null}
                    </div>

                    <div className={styles.formSection}>
                      <label className="label">Adresse :</label>
                      <Field
                        className={`secondary-input ${
                          errors.address && touched.address
                            ? "secondary-input-error"
                            : ""
                        }`}
                        name="address"
                        type="text"
                        placeholder="Adresse"
                        defaultValue={association ? association.address : ""}
                      />
                      {errors.address && touched.address ? (
                        <div className="error-message">{errors.address}</div>
                      ) : null}
                    </div>
                    <div
                      className={`row-space-between align-flex-start ${styles.row}`}
                    >
                      <div
                        className={`${styles.formSection} ${styles.separator} two-thirds ${styles.city}`}
                      >
                        <label className="label">Ville :</label>
                        <Field
                          className={`secondary-input ${
                            errors.city && touched.city
                              ? "secondary-input-error"
                              : ""
                          }`}
                          name="city"
                          type="text"
                          placeholder="Ville"
                          defaultValue={association ? association.city : ""}
                        />
                        {errors.city && touched.city ? (
                          <div className="error-message">{errors.city}</div>
                        ) : null}
                      </div>
                      <div
                        className={`${styles.formSection} one-third ${styles.postCode}`}
                      >
                        <label className="label">Code postal :</label>
                        <Field
                          className={`secondary-input ${
                            errors.post_code && touched.post_code
                              ? "secondary-input-error"
                              : ""
                          }`}
                          name="post_code"
                          type="text"
                          placeholder="Code postal"
                          defaultValue={
                            association ? association.post_code : ""
                          }
                        />
                        {errors.post_code && touched.post_code ? (
                          <div className="error-message">
                            {errors.post_code}
                          </div>
                        ) : null}
                      </div>
                    </div>
                    <div className={`${styles.formSection} two-thirds`}>
                      <label className="label">N de SIRET :</label>
                      <Field
                        className={`secondary-input ${
                          errors.siret && touched.siret
                            ? "secondary-input-error"
                            : ""
                        }`}
                        name="siret"
                        type="text"
                        placeholder="N de SIRET"
                        defaultValue={association ? association.siret : ""}
                      />
                      {errors.siret && touched.siret ? (
                        <div className="error-message">{errors.siret}</div>
                      ) : null}
                    </div>
                    <div className={`${styles.formSection} two-thirds`}>
                      <label className="label">N d'association :</label>
                      <Field
                        className={`secondary-input ${
                          errors.assoNumber && touched.assoNumber
                            ? "secondary-input-error"
                            : ""
                        }`}
                        name="assoNumber"
                        type="text"
                        placeholder="N d'association"
                        defaultValue={association ? association.assoNumber : ""}
                      />
                      {errors.assoNumber && touched.assoNumber ? (
                        <div className="error-message">{errors.assoNumber}</div>
                      ) : null}
                    </div>
                    <button
                      className={`primary-button ${styles.submit}`}
                      type="submit"
                    >
                      Modifier les informations
                    </button>
                  </Form>
                )}
              </Formik>
            </div>
            <div className={styles.infoContainer}>
              <Formik
                initialValues={{
                  gender: owner.gender,
                  firstname: owner.firstname,
                  lastname: owner.lastname,
                  email: owner.email,
                  phone: owner.phone,
                  address: owner.address,
                  city: owner.city,
                  post_code: owner.post_code,
                }}
                validationSchema={ownerSchema}
                enableReinitialize={true}
                onSubmit={(values) => {
                  setNotification({
                    state: "shown",
                    type: "loading",
                    message: "Modification en cours...",
                  });
                  api
                    .put("/associations/update/owner", values)
                    .then(() =>
                      setNotification({
                        state: "shown",
                        type: "success",
                        message: "Changements appliqus !",
                      })
                    )
                    .catch((err) => {
                      setNotification({
                        state: "shown",
                        type: "error",
                        message: err.response.data.error,
                      });
                    });
                }}
              >
                {({ errors, touched }) => (
                  <Form>
                    <h2>Son reprsentant lgal :</h2>
                    <div className={styles.formSection}>
                      <label className="label">Civilit :</label>
                      <div className={styles.gender}>
                        <div className={styles.radio}>
                          <Field type="radio" name="gender" id="M" value="M" />
                          <label htmlFor="M">M</label>
                        </div>

                        <div className={styles.radio}>
                          <Field
                            type="radio"
                            name="gender"
                            id="Mme"
                            value="Mme"
                          />
                          <label htmlFor="Mme">Mme</label>
                        </div>

                        <div className={styles.radio}>
                          <Field
                            type="radio"
                            name="gender"
                            id="Mlle"
                            value="Mlle"
                          />
                          <label htmlFor="Mlle">Mlle</label>
                        </div>
                      </div>
                      {errors.civilite && touched.civilite ? (
                        <div className={styles.errorMessage}>
                          {errors.civilite}
                        </div>
                      ) : null}
                    </div>
                    <div
                      className={`row-space-between align-flex-start ${styles.row}`}
                    >
                      <div
                        className={`${styles.formSection} ${styles.separator}`}
                      >
                        <label className="label">Nom :</label>
                        <Field
                          className={`secondary-input ${
                            errors.lastname && touched.lastname
                              ? "secondary-input-error"
                              : ""
                          }`}
                          name="lastname"
                          type="text"
                          placeholder="Nom"
                          defaultValue={owner ? owner.lastname : ""}
                        />
                        {errors.lastname && touched.lastname ? (
                          <div className="error-message">{errors.lastname}</div>
                        ) : null}
                      </div>
                      <div className={styles.formSection}>
                        <label className="label">Prnom :</label>
                        <Field
                          className={`secondary-input ${
                            errors.firstname && touched.firstname
                              ? "secondary-input-error"
                              : ""
                          }`}
                          name="firstname"
                          type="text"
                          placeholder="Prnom"
                          defaultValue={owner ? owner.firstname : ""}
                        />
                        {errors.firstname && touched.firstname ? (
                          <div className="error-message">
                            {errors.firstname}
                          </div>
                        ) : null}
                      </div>
                    </div>
                    <div className={styles.formSection}>
                      <label className="label">Email :</label>
                      <Field
                        className={`secondary-input ${
                          errors.email && touched.email
                            ? "secondary-input-error"
                            : ""
                        }`}
                        name="email"
                        type="text"
                        placeholder="Email"
                        defaultValue={owner ? owner.email : ""}
                      />
                      {errors.email && touched.email ? (
                        <div className="error-message">{errors.email}</div>
                      ) : null}
                    </div>
                    <div className={styles.formSection}>
                      <label className="label">Tlphone :</label>
                      <Field
                        className={`secondary-input ${
                          errors.phone && touched.phone
                            ? "secondary-input-error"
                            : ""
                        }`}
                        name="phone"
                        type="text"
                        placeholder="N de tlphone"
                        defaultValue={owner ? owner.phone : ""}
                      />
                      {errors.phone && touched.phone ? (
                        <div className="error-message">{errors.phone}</div>
                      ) : null}
                    </div>
                    <div className={styles.formSection}>
                      <label className="label">Adresse :</label>
                      <Field
                        className={`secondary-input ${
                          errors.address && touched.address
                            ? "secondary-input-error"
                            : ""
                        }`}
                        name="address"
                        type="text"
                        placeholder="Adresse"
                        defaultValue={owner ? owner.address : ""}
                      />
                      {errors.address && touched.address ? (
                        <div className="error-message">{errors.address}</div>
                      ) : null}
                    </div>
                    <div
                      className={`row-space-between align-flex-start ${styles.row}`}
                    >
                      <div
                        className={`${styles.formSection} ${styles.separator} ${styles.city} two-thirds`}
                      >
                        <label className="label">Ville :</label>
                        <Field
                          className={`secondary-input ${
                            errors.city && touched.city
                              ? "secondary-input-error"
                              : ""
                          }`}
                          name="city"
                          type="text"
                          placeholder="Ville"
                          defaultValue={owner ? owner.city : ""}
                        />
                        {errors.city && touched.city ? (
                          <div className="error-message">{errors.city}</div>
                        ) : null}
                      </div>
                      <div
                        className={`${styles.formSection} one-third ${styles.postCode}`}
                      >
                        <label className="label">Code postal :</label>
                        <Field
                          className={`secondary-input ${
                            errors.post_code && touched.post_code
                              ? "secondary-input-error"
                              : ""
                          }`}
                          name="post_code"
                          type="text"
                          placeholder="Code postal"
                          defaultValue={owner ? owner.post_code : ""}
                        />
                        {errors.post_code && touched.post_code ? (
                          <div className="error-message">
                            {errors.post_code}
                          </div>
                        ) : null}
                      </div>
                    </div>
                    <button
                      className={`primary-button ${styles.submit}`}
                      type="submit"
                    >
                      Modifier les informations
                    </button>
                  </Form>
                )}
              </Formik>
            </div>
          </div>
        ) : null}
        <div className={styles.settingsFooter}>
          <h2>Oprations sur votre association :</h2>
          <div className={styles.footerRow}>
            <button
              className={`primary-button ${styles.separator}`}
              onClick={handleLogout}
            >
              Se dconnecter
            </button>
            <button className="red-outlined-button" onClick={handleDelete}>
              Supprimer l'association
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Settings;
