import styles from "./AnimalCard.module.css";
import LikeIcon from "../LikeIcon/LikeIcon.jsx";
import { ReactComponent as Shape } from "./shape.svg";
import { ReactComponent as Arrow } from "./arrow.svg";
import Moment from "react-moment";
import "moment/locale/fr";

const AnimalCard = (props) => {
  return (
    <div className={styles.card}>
      <div className={`${styles.cardHead} `}>
        <Shape
          className={`${styles.shape} ${
            props.category === "cat" ? styles.cat : ""
          } ${props.category === "dog" ? styles.dog : ""} ${
            props.category === "rabbit" ? styles.rabbit : ""
          } ${props.category === "bird" ? styles.bird : ""} ${
            props.category === "rodent" ? styles.rodent : ""
          }`}
        />
        <div className={styles.cardImgContainer}>
          {
            <img
              className={styles.cardImg}
              src={props.imageLink}
              alt={"image de " + props.name}
            />
          }
        </div>
      </div>
      <div className={styles.cardBody}>
        <div className={styles.cardContentHeader}>
          <h1 className={styles.name}>{props.name}</h1>
          <h2 className={styles.identity}>
            <span
              className={`${
                props.gender === "male"
                  ? styles.genderMale
                  : styles.genderFemale
              }`}
            >
              {props.gender === "male" && "Mle "}
              {props.gender === "female" && "Femelle "}
            </span>
            <span className={styles.age}>
              -{" "}
              <Moment locale="fr" fromNow ago>
                {props.birthDate}
              </Moment>
            </span>
          </h2>
          <h3 className={styles.city}>{props.city}</h3>
        </div>
        <div className={styles.cardContentBody}>
          <div className="row-space-between align-flex-end">
            <div>
              <h2 className={styles.caracteristicsTitle}>Je suis :</h2>
              <ul className={styles.caracteristicsList}>
                <li>{props.about1}</li>
                <li>{props.about2}</li>
                <li>{props.about3}</li>
              </ul>
            </div>

            <div className="row-space-between">
              {/* <div className={styles.like}>
                <LikeIcon
                  className={styles.likeIcon}
                  className2={styles.likeIconInterior}
                ></LikeIcon>
              </div> */}

              <a className={styles.see} href={props.link}>
                <Arrow />
              </a>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default AnimalCard;
