import styles from "./Listings.module.css";

import { useContext, useEffect, useState } from "react";
import { Link } from "react-router-dom";

import notificationContext from "../../../../../contexts/notificationContext";

import { ReactComponent as SeeIcon } from "./see.svg";
import { ReactComponent as DeleteIcon } from "./delete.svg";
import { ReactComponent as EditIcon } from "./edit.svg";

import Cookies from "js-cookie";

import Moment from "react-moment";
import "moment/locale/fr";
import api from "./../../../../../services/api";

const Listings = () => {
  const { setNotification } = useContext(notificationContext);

  const [listings, setListings] = useState([]);
  const [success, setSuccess] = useState(false);
  const [isComplete, setIsComplete] = useState(false);

  const handleDelete = async (listing) => {
    const result = window.confirm(
      "Etes-vous sur de vouloir supprimer cette annonce ?"
    );
    if (result === true) {
      await api
        .delete(`/associations/listings/delete/listing=${listing.id}`)
        .then((res) => {
          if (res.data.success) {
            setSuccess(true);
            setNotification({
              state: "shown",
              type: "success",
              message: "Annonce supprime avec succs",
            });
          }
          setSuccess(false);
        });
    }
  };

  useEffect(() => {
    api.get(`/associations/public/`).then((res) => {
      if (res.data.isComplete) {
        setIsComplete(true);
      }
    });
  }, []);

  useEffect(() => {
    api.get(`/association`).then((res) => {
      document.title = `${res.data.name} | Annonces`;
    });
  });

  useEffect(() => {
    api
      .get(`/association/${Cookies.get("association_id")}/listings`)
      .then((res) => {
        setListings(res.data.listings);
      });
  }, [success, setListings]);

  return (
    <div className={styles.content}>
      <h1 className={styles.pageTitle}>Vos annonces :</h1>

      <div>
        {isComplete ? (
          listings.length > 0 ? (
            <div className={styles.listHeader}>
              <span className={styles.headerText}>
                Vous avez {listings.length}{" "}
                {listings.length > 1 ? "annonces" : "annonce"} en ligne
              </span>
              <Link
                to="/associations/panel/create-listing"
                className="primary-button"
              >
                <span>Crer une annonce</span>
              </Link>
            </div>
          ) : (
            <div className={styles.listHeader}>
              <span className={styles.headerText}>
                Vous n'avez publi aucune annonce
              </span>
              <Link
                to="/associations/panel/create-listing"
                className="primary-button"
              >
                <span>Crer une annonce</span>
              </Link>
            </div>
          )
        ) : (
          <div className={styles.listHeader}>
            <span className={styles.headerText}>
              Vous devez complter votre profil public pour pouvoir poster des
              annonces
            </span>
            <Link to="/associations/panel/profile" className="primary-button">
              <span>Complter le profil</span>
            </Link>
          </div>
        )}
        <div>
          {isComplete && listings.length > 0
            ? listings.map((listing) => {
                return (
                  <div key={listing.id} className={styles.listing}>
                    <div
                      className={`${styles.listingColor} ${
                        listing.category === "cat" ? styles.catColor : null
                      } ${
                        listing.category === "dog" ? styles.dogColor : null
                      } ${
                        listing.category === "rabbit"
                          ? styles.rabbitColor
                          : null
                      } ${
                        listing.category === "bird" ? styles.birdColor : null
                      } ${
                        listing.category === "rodent"
                          ? styles.roddentColor
                          : null
                      }`}
                    ></div>
                    <div className={styles.listingContent}>
                      <div className={styles.listingLeft}>
                        <img
                          className={styles.listingImage}
                          src={listing.image1}
                          alt={listing.name}
                        />
                        <div className={styles.listingInfo}>
                          <Link
                            to={`/listing/${listing.id}`}
                            className={styles.listingTitle}
                          >
                            {listing.name}
                          </Link>
                          <h4 className={styles.listingAttributes}>
                            <span
                              className={`${
                                listing.gender === "male"
                                  ? styles.genderMale
                                  : styles.genderFemale
                              }`}
                            >
                              {listing.gender === "male" && "Mle "}
                              {listing.gender === "female" && "Femelle "}
                            </span>
                            <span>
                              -{" "}
                              <Moment locale="fr" fromNow ago>
                                {listing.birth_date}
                              </Moment>
                            </span>
                          </h4>
                        </div>
                        <div className={styles.separator}></div>
                        <span className={styles.price}>{listing.price} </span>
                      </div>
                      <div className={styles.listingButtons}>
                        <Link
                          to={`/listing/${listing.id}`}
                          className={styles.listingBtn}
                        >
                          <SeeIcon />
                        </Link>
                        <Link
                          to={`/associations/panel/listing/edit/${listing.id}`}
                          className={styles.listingBtn}
                        >
                          <EditIcon />
                        </Link>
                        <div
                          onClick={() => handleDelete(listing)}
                          className={styles.listingBtn}
                        >
                          <DeleteIcon />
                        </div>
                      </div>
                    </div>
                  </div>
                );
              })
            : null}
        </div>
      </div>
    </div>
  );
};

export default Listings;
