import styles from "./Association.module.css";
import { useEffect, useState, useContext } from "react";
import { useParams, Link } from "react-router-dom";

import { ReactComponent as Building } from "./building.svg";
import { ReactComponent as Mail } from "./mail.svg";
import { ReactComponent as Phone } from "./phone.svg";
import { ReactComponent as Website } from "./Web.svg";
import { ReactComponent as Facebook } from "./Facebook.svg";
import { ReactComponent as Twitter } from "./Twitter.svg";
import { ReactComponent as Instagram } from "./Instagram.svg";

import { currentPageContext } from "../../../../contexts/shared/currentPageContext";

import Moment from "react-moment";
import "moment/locale/fr";
import api from "./../../../../services/api";

const Association = () => {
  const { setCurrentPage } = useContext(currentPageContext);
  const [association, setAssociation] = useState(null);
  const { id } = useParams();

  useEffect(() => {
    api.get(`/associations/public/${id}`).then((res) => {
      setAssociation(res.data);
    });
  }, [setAssociation]);

  useEffect(() => {
    setCurrentPage("adopt-me");
  }, []);

  if (association) {
    return (
      <div className={styles.content}>
        <div className={styles.assoHeader}>
          <div className={styles.imageContainer}>
            <img className={styles.image} src={association.image} />
          </div>

          <div className={styles.assoHead}>
            <h1 className={styles.name}>{association.name}</h1>
            <span>
              Membre depuis le{" "}
              <Moment locale="fr" format="Do MMMM YYYY">
                {association.createdAt}
              </Moment>
            </span>
          </div>
        </div>
        <div className={styles.assoBody}>
          <section className={styles.section}>
            <h2 className={styles.title}>Description :</h2>
            <p className={styles.description}>{association.description}</p>
          </section>
          <section className={styles.section}>
            <h2 className={styles.title}>Nous contacter :</h2>
            <ul className={styles.contactList}>
              <li className={styles.contact}>
                <Mail />
                <span>{association.email}</span>
              </li>
              <li className={styles.contact}>
                <Phone />
                <span>{association.phone}</span>
              </li>
              <li className={styles.contact}>
                <Building />
                <span>{`${association.address}, ${association.city} ${association.post_code}`}</span>
              </li>
            </ul>
          </section>
          {(association.website ||
            association.facebook ||
            association.instagram ||
            association.twitter) && (
            <section className={styles.section}>
              <h2 className={styles.title}>Nous suivre :</h2>
              <div className={styles.socials}>
                {association.website ? (
                  <Link
                    to={association.website}
                    target="_blank"
                    rel="noopener noreferrer"
                    className={styles.social}
                  >
                    <Website />
                    <span>Site web</span>
                  </Link>
                ) : null}
                {association.facebook ? (
                  <Link
                    to={association.facebook}
                    target="_blank"
                    rel="noopener noreferrer"
                    className={styles.social}
                  >
                    <Facebook />
                    <span>Facebook</span>
                  </Link>
                ) : null}
                {association.instagram ? (
                  <Link
                    to={association.instagram}
                    target="_blank"
                    rel="noopener noreferrer"
                    className={styles.social}
                  >
                    <Instagram />
                    <span>Instagram</span>
                  </Link>
                ) : null}
                {association.twitter ? (
                  <Link
                    to={association.twitter}
                    target="_blank"
                    rel="noopener noreferrer"
                    className={styles.social}
                  >
                    <Twitter />
                    <span>Twitter</span>
                  </Link>
                ) : null}
              </div>
            </section>
          )}
        </div>
      </div>
    );
  } else {
    return <div></div>;
  }
};

export default Association;
