import styles from "./Notification.module.css";

import { ReactComponent as Success } from "./success.svg";
import { ReactComponent as Error } from "./error.svg";
import { ReactComponent as Info } from "./info.svg";

import NotificationContext from "../../../contexts/notificationContext";

import { useContext, useEffect } from "react";

const Notification = () => {
  const { notification, setNotification } = useContext(NotificationContext);

  useEffect(() => {
    if (
      (notification.type === "success" ||
        notification.type === "info" ||
        notification.type === "error") &&
      notification.state !== "hidden"
    ) {
      setTimeout(() => {
        setNotification({
          state: "hidden",
          type: notification.type,
          message: notification.message,
        });
      }, 5000);
    }
  }, [notification]);

  return (
    <div
      className={`${styles.notification} ${
        notification.state === "hidden" ? styles.vanish : ""
      } ${notification.state === "shown" ? styles.active : ""} ${
        notification.type === "success" ? styles.success : ""
      } ${notification.type === "info" ? styles.info : ""} ${
        notification.type === "loading" ? styles.loading : ""
      } ${notification.type === "error" ? styles.error : ""}`}
    >
      <div className={styles.notificationContent}>
        <div className={styles.icon}>
          {notification.type === "success" ? <Success /> : null}
          {notification.type === "error" ? <Error /> : null}
          {notification.type === "info" || notification.type === "loading" ? (
            <Info />
          ) : null}
        </div>
        <span className={styles.text}>{notification.message}</span>
      </div>
      <div className={styles.progressBarContainer}>
        <div className={styles.progressBar}></div>
      </div>
    </div>
  );
};

export default Notification;
