import { useState, useEffect } from "react";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import api from "./services/api";
import Cookies from "js-cookie";

import Header from "./components/shared/Header/Header";
import Footer from "./components/shared/Footer/Footer";
import Home from "./components/pages/shared/Home";
import AdoptMe from "./components/pages/AdoptMe/AdoptMe";
import Listing from "./components/pages/clients/Listing/Listing";
import Association from "./components/pages/clients/Association/Association";

import Register from "./components/pages/clients/Auth/Register";
import ForgotPassword from "./components/pages/clients/Auth/ForgotPassword";
import ResetPassword from "./components/pages/clients/Auth/ResetPassword";

import userContext from "./contexts/userContext";
import authContext from "./contexts/authContext";
import { AssociationContext } from "./contexts/associations/assoContext";
import notificationContext from "./contexts/notificationContext";

import ProtectedRoute from "./components/routes/ProtectedRoute";
import Account from "./components/pages/clients/Auth/Account";

import CreateAssociation from "./components/pages/associations/Auth/CreateAssociation";
import Login from "./components/pages/clients/Auth/Login";

import AssociationPanel from "./components/pages/associations/Panel/AssociationPanel";
import Notification from "./components/shared/Notification/Notification";
import DeletePopUp from "./components/shared/DeletePopUp/DeletePopUp";
import { deletePopUpContext } from "./contexts/shared/deletePopUpContext";
import { currentPageContext } from "./contexts/shared/currentPageContext";
import NotFound from "./components/pages/shared/NotFound/NotFound";

function App() {
  const [user, setUser] = useState({});
  const [currentPage, setCurrentPage] = useState("home");
  const [association, setAssociation] = useState({});
  const [notification, setNotification] = useState({
    state: "",
    type: "",
    message: "",
  });
  const [deletePopUp, setDeletePopUp] = useState({
    isShown: false,
    title: "",
    text: "",
    textToEnter: "",
    link: "",
    buttonText: "",
  });

  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => (document.title = "Animazone"));

  useEffect(() => {
    if (Cookies.get("authenticated")) {
      api.get("/user").then((res) => {
        setUser(res.data);
        setIsAuthenticated(true);
      });
    }

    if (isAuthenticated) {
      let hasAssociation = Cookies.get("hasAssociation");
      if (hasAssociation) {
        api.get(`/association`).then(async (res) => {
          setAssociation(res.data);
        });
      }
    }
    setIsLoading(false);
  }, [isAuthenticated]);

  return isLoading ? (
    <div className="App">Loading...</div>
  ) : (
    <div className="App">
      <currentPageContext.Provider value={{ currentPage, setCurrentPage }}>
        <notificationContext.Provider value={{ notification, setNotification }}>
          <deletePopUpContext.Provider value={{ deletePopUp, setDeletePopUp }}>
            <userContext.Provider value={{ user, setUser }}>
              <AssociationContext.Provider
                value={{ association, setAssociation }}
              >
                <authContext.Provider
                  value={{ isAuthenticated, setIsAuthenticated }}
                >
                  <Router>
                    <Header />
                    <div className="content-page">
                      <Switch>
                        <Route exact path="/home" component={Home} />
                        <Route exact path="/" component={Home} />

                        <Route exact path="/adopt-me" component={AdoptMe} />
                        <Route exact path="/listing/:id" component={Listing} />
                        <Route
                          exact
                          path="/association/:id"
                          component={Association}
                        />

                        <Route exact path="/register" component={Register} />
                        <Route exact path="/login" component={Login} />
                        <Route
                          exact
                          path="/forgot-password"
                          component={ForgotPassword}
                        />
                        <Route
                          exact
                          path="/reset-password"
                          component={ResetPassword}
                        />

                        <ProtectedRoute path="/account">
                          <Account />
                        </ProtectedRoute>
                        <ProtectedRoute path="/associations/create">
                          <CreateAssociation />
                        </ProtectedRoute>

                        <ProtectedRoute path="/associations/panel">
                          <AssociationPanel />
                        </ProtectedRoute>

                        <Route path="*" component={NotFound} />
                      </Switch>
                      <DeletePopUp
                        isShown={deletePopUp.isShown}
                        title={deletePopUp.title}
                        text={deletePopUp.text}
                        textToEnter={deletePopUp.textToEnter}
                        link={deletePopUp.link}
                        buttonText={deletePopUp.buttonText}
                      />

                      <Notification
                        state={notification.state}
                        type={notification.type}
                        message={notification.message}
                      />
                    </div>

                    {currentPage === "association" ? null : <Footer />}
                  </Router>
                </authContext.Provider>
              </AssociationContext.Provider>
            </userContext.Provider>
          </deletePopUpContext.Provider>
        </notificationContext.Provider>
      </currentPageContext.Provider>
    </div>
  );
}

export default App;
